<script lang="ts">
  import { LockLockedIcon } from "$lib/icons";
  import Menu from './common/Menu.svelte';
  import TextButton from './common/TextButton.svelte';
  import { authState, removeReply, setEditingReplyId, addToast } from "./states";
  import type { Reply } from "./api";

  const { reply } = $props<{ reply: Reply }>();
  
  function handleEdit(event: Event) {
    event.stopPropagation();
    setEditingReplyId(reply.id!);
  }
  
  async function handleDelete(event: Event) {
    event.stopPropagation();
    if (confirm('Are you sure you want to delete this reply?')) {
      const success = await removeReply(reply.id!);
      if (success) {
        addToast({
          message: "Reply deleted successfully",
          type: "success",
          dismissible: true,
          timeout: 3000,
        });
      } else {
        addToast({
          message: "Failed to delete reply",
          type: "error",
          dismissible: true,
          timeout: 5000,
        });
      }
    }
  }

  function handleMentionAuthor() {
    console.log('Mention author:');
    // Logic for mentioning the author would go here
  }
</script>

<div class="reply-controls">
  {#if reply?.privacy === 'private'}
    <LockLockedIcon size={16} />
  {/if}
  <Menu>
    {#if reply?.profile?.id === authState.user?.id}
    <TextButton 
    stopPropagation
    onclick={handleEdit}
    ariaLabel="Edit reply"
    size="xs"
    >
    Edit
  </TextButton>
  <TextButton 
      stopPropagation
      onclick={handleDelete}
      ariaLabel="Delete reply"
      size="xs"
      >
      Delete
    </TextButton>
    {:else}
    <TextButton
    stopPropagation
    onclick={handleMentionAuthor}
    ariaLabel="Mention author"
    size="xs"
    >
    Mention author
  </TextButton>
    {/if}
  </Menu>
</div>

<style>
  .reply-controls {
    margin-top: 0.25rem;
    display: flex;
    justify-content: flex-end;
  }
</style>
