<script lang="ts">
  import Cursor from "../icons/Cursor.svelte";
  import { onMount, onDestroy } from "svelte";

  let isEnabled = $state(false);
  let mouseX = $state(0);
  let mouseY = $state(0);

  function handleMouseMove(e: MouseEvent) {
    if (isEnabled) {
      mouseX = e.clientX;
      mouseY = e.clientY;
    }
  }

  function toggleDebugger() {
    isEnabled = !isEnabled;
  }

  onMount(() => {
    window.addEventListener("mousemove", handleMouseMove);
  });

  onDestroy(() => {
    window.removeEventListener("mousemove", handleMouseMove);
  });
</script>

<!-- Toggle Button -->
<button
  class="debug-toggle"
  class:active={isEnabled}
  onclick={toggleDebugger}
  title="Toggle mouse position debugger"
>
  <Cursor size="16" />
</button>

<!-- Floating Coordinate Display -->
{#if isEnabled}
  <div
    class="mouse-coordinates"
    style="left: {mouseX + 10}px; top: {mouseY + 10}px;"
  >
    <div class="coordinate">x: {mouseX}</div>
    <div class="coordinate">y: {mouseY}</div>
  </div>
{/if}

<style>
  button {
    background-color: transparent;
    border: none;
    border-radius: 0.5rem;
    height: 1.5rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  button:hover {
    background-color: var(--color-grey-200);
  }
  .active {
    color: var(--clr-info-a0);
    background-color: var(--color-grey-200);
  }

  .mouse-coordinates {
    position: fixed;
    z-index: 9998;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-family: monospace;
    font-size: 12px;
    pointer-events: none;
    white-space: nowrap;
    backdrop-filter: blur(4px);
  }

  .coordinate {
    line-height: 1.2;
  }
</style>
