<script lang="ts">
  import { History, Privacy } from "./api-client"; // @deprecated
  import { type Annotation } from "./api";
  import {
    setCurrentAnnotationId,
    removeAnnotation,
    annotationsState,
    editAnnotation,
    authState,
    addToast,
    setEditingAnnotationId,
    setDraftAnnotation,
  } from "./states";
  import TextButton from "./common/TextButton.svelte";
  import Menu from "./common/Menu.svelte";
  import { LockLockedIcon } from "$lib/icons";
  import { push } from "svelte-spa-router";

  const { annotation } = $props<{ annotation: Annotation }>();

  function handleOpenDetails() {
    if (annotationsState.currentAnnotationId === annotation.id) {
    } else {
      setCurrentAnnotationId(annotation.id);
    }
  }

  function handleReply(event: Event) {
    event.stopPropagation();
    push(`/annotation/${annotation?.id}`);
  }

  function handleEdit(event: Event) {
    event.stopPropagation();
    setEditingAnnotationId(annotation.id!);
  }

  function handleDelete(event: Event) {
    event.stopPropagation();
    if (confirm("Are you sure you want to delete this annotation?")) {
      removeAnnotation(annotation?.id!);
      setCurrentAnnotationId(null);
    }
  }

  function handleShare(event: Event) {
    event.stopPropagation();
    console.log("[not implemented] Share annotation:", annotation?.id);
    addToast({
      message: "[Beta] Sharing not implemented, yet.",
      type: "warning",
      dismissible: true,
      timeout: 5000,
    });
  }

  async function handleArchive(event: Event) {
    event.stopPropagation();
    await editAnnotation(annotation?.id!, { history: History.Archived });
  }
</script>

<div class="annotation-controls" role="button" tabindex="0">
  {#if annotationsState.currentAnnotationId !== annotation.id}
    <TextButton on:click={handleReply} size="xs">Reply</TextButton>
  {/if}
  {#if annotation?.privacy === Privacy.Private}
    <LockLockedIcon size={16} />
  {/if}

  <Menu label="Annotation options">
    {#if annotation?.profile?.id === authState.user?.id}
      <TextButton onclick={handleEdit} size="xs">Edit</TextButton>
      <TextButton onclick={handleDelete} size="xs">Delete</TextButton>
      {#if annotation?.history !== History.Archived}
        <TextButton onclick={handleArchive} size="xs">Archive</TextButton>
      {/if}
    {/if}
    <TextButton onclick={handleShare} size="xs">Share</TextButton>
  </Menu>
</div>

<style>
  .annotation-controls {
    display: inline-flex;
    gap: 0.5rem;
    font-size: 0.8em;
    align-items: center;
  }
</style>
