// Wrapper for the auto-generated API client
import { createConfig } from '@hey-api/client-fetch';
import { config } from '../config'; 
import { settingsState } from '../states';
import { client } from './client/client.gen';

// Create a custom fetch function that includes credentials
const fetchWithCredentials = async (request: Request): Promise<Response> => {
  return fetch(request, {
    credentials: 'include',
    headers: {
      ...Object.fromEntries(request.headers.entries()),
      'Content-Type': 'application/json',
    }
  });
};

if(config.serverUrl || settingsState.serverUrl) {
  const baseUrl = `${settingsState.serverUrl || config.serverUrl}${config.apiBaseUrl}`;
  client.setConfig(createConfig({ 
    baseUrl,
    // Use custom fetch function that includes credentials
    fetch: fetchWithCredentials
  }));
} else {
  console.warn('Server URL is not set. The first api call has to set it to either config or settingsState');
}

export const api = {
  setServerUrl(serverUrl: string): void {
    client.setConfig(createConfig({ 
      baseUrl: `${serverUrl}${config.apiBaseUrl}`,
      // Use custom fetch function that includes credentials
      fetch: fetchWithCredentials
    }));
  },
};
export * from './client/types.gen';
export * from './client/sdk.gen';
export { client };