import { createSubscriber, deleteSubscription, getSubscribers, type Subscriber } from "../api";
import { authState, instanceState } from "./../states";
import type { ApiError } from "../types/errors";
import { getApiError } from "../utils/errors";

export let subscriptionState = $state({
  subscription: null as Subscriber | null,
  error: null as ApiError | null,
  isLoading: false,
});

export async function loadSubscription(): Promise<Subscriber | null > {
  subscriptionState.isLoading = true;
  subscriptionState.error = null;
  
  try {
    console.log("Load Subscription");

    const { data, error } = await getSubscribers({
      query:{
        instance_id: instanceState.instance?.id!,
        profile_id: authState.user?.id!,
      }
    });
    if (error) {
      throw error;
    }
    const { subscribers } = data || {};
    subscriptionState.isLoading = false;
    const subscription = subscribers?.[0] || null;
    if (subscription) {
      subscriptionState.subscription = subscription;
    }
    return subscription;
  } catch (err: unknown) {
    subscriptionState.error = getApiError((err as ApiError).code, {scope: "client", table: "subscriber"});
    subscriptionState.isLoading = false;
    return null;
  }
}

export async function subscribe(): Promise<Subscriber | null> {
  subscriptionState.isLoading = true;
  subscriptionState.error = null;
  
  try {
    console.log("Subscribe");

    const { data:subscriber, error } = await createSubscriber({
      body:{
        instance_id: instanceState.instance?.id!,
        profile_id: authState.user?.id!,
      }
    });

    subscriptionState.isLoading = false;
    if (subscriber) {
      subscriptionState.subscription = subscriber;
      return subscriber;
    }
    if (error as unknown) {
      subscriptionState.error = getApiError((error as ApiError).code);
    }
    return null;
  } catch (err: unknown) {
    subscriptionState.error = getApiError((err as ApiError).code);
    subscriptionState.isLoading = false;
    return null;
  }
}

export async function unsubscribe(): Promise<boolean> {
  subscriptionState.isLoading = true;
  subscriptionState.error = null;
  
  try {
    console.log("Unsubscribe");

    const { error } = await deleteSubscription({
      body:{
        instance_id: instanceState.instance?.id!,
        profile_id: authState.user?.id!,
      }
    });
    subscriptionState.isLoading = false;
   
    if (error) throw error;
    else {
      subscriptionState.subscription = null;
      return true;
    }
  } catch (err: unknown) {
    subscriptionState.error = getApiError((err as ApiError).code);
    subscriptionState.isLoading = false;
    return false;
  }
}