<script lang="ts">
  import { createEventDispatcher } from "svelte";
  import Elipsis from "../icons/Elipsis.svelte";
  import { onClickOutside } from "runed";

  // Props
  let { label = "More options" } = $props();
  
  let open = $state(false);
  
  const dispatch = createEventDispatcher();


    let dropdown = $state<HTMLDivElement>()!;
 
 const clickOutside = onClickOutside(
   () => dropdown,
   () => {
     open = false;
     clickOutside.stop();
   },
   { immediate: false }
 );

 function openDialog() {
   open = true;
  //  clickOutside.start();
 }

 function closeDialog() {
   open = false;
  //  clickOutside.stop();
 }

  function toggleDropdown(event: Event) {
    event.stopPropagation();
    if (open) {
      closeDialog();
    } else {
      openDialog();
    }
  }
  
  $effect(() => {
    dispatch('toggle', { open: open });
  })
</script>

<div class="dropdown-container {open ? 'open' : ''}" >
  <button 
    class="menu-toggle" 
    onclick={toggleDropdown}
    aria-label={label}
    aria-expanded={open}
    aria-haspopup="true"
  >
    <Elipsis />
  </button>
  

    <div class="dropdown-menu" bind:this={dropdown} >
      <slot></slot>
    </div>
</div>

<style>
  .dropdown-container {
    position: relative;
  }

  .dropdown-menu {
    position: absolute;
    right: 0;
    top: 100%;
    margin-top: 0.25rem;
    background-color: var(--background-color);
    border-radius: 0.5rem;
    box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.1);
    padding: 1rem;
    border: 1px solid var(--color-grey-700);
    z-index: 10;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    min-width: 6rem;
    animation: fade-in 0.2s ease forwards;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(-0.25rem);
    transition: opacity 0.2s ease;
  }
  .open .dropdown-menu {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translateY(0);
  }

  .menu-toggle {
    background-color: transparent;
    padding: 0;
    line-height: 1.2rem;
    border: 0;
    border-radius: .25rem;
    height: 1.5rem;
    width: 1.5rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .menu-toggle :global(svg) {
    width: 1rem;
    height: 1rem;
  }
  .menu-toggle:hover {
    background: var(--color-grey-100);
  }

  @keyframes fade-in {
    from {
      opacity: 0;
      transform: translateY(-0.25rem);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
</style>
