/**
 * Sanitizes a URL by removing potentially sensitive information while preserving essential query parameters.
 * 
 * Removes:
 * - Authentication credentials (username:password@)
 * - Common sensitive tokens (auth, token, key, api_key, jwt, access_token, etc.)
 * - Session identifiers (PHPSESSID, etc.)
 * 
 * Preserves:
 * - Domain and path
 * - Essential query parameters (page, entry, id, category, tag, etc.)
 */
export function sanitizeUrl(url: string): string {
    try {
      const urlObj = new URL(url);
      
      // Remove username and password if present
      urlObj.username = "";
      urlObj.password = "";

      // Remove hash
      urlObj.hash = "";
      
      // Get all query parameters
      const params = new URLSearchParams(urlObj.search);
      const newParams = new URLSearchParams();
      
      // List of parameter names to preserve
      const preserveParams = [
        "page", "p", "entry", "id", "post_id", "category", "cat", "tag", 
        "q", "s", "search", "query", "filter", "sort", "order", "limit", 
        "offset", "per_page", "view", "type", "format", "lang", "language"
      ];
      
      // List of parameter prefixes to remove (common auth tokens)
      const sensitiveParamPrefixes = [
        "auth", "token", "key", "api", "jwt", "access", "refresh", 
        "secret", "pass", "session", "oauth", "login", "user", "uid"
      ];
      
      // Process each parameter
      params.forEach((value, key) => {
        // Check if this is a parameter we want to preserve
        if (preserveParams.includes(key.toLowerCase())) {
          newParams.append(key, value);
          return;
        }
        
        // Check if this parameter might contain sensitive information
        const lowerKey = key.toLowerCase();
        const isSensitive = sensitiveParamPrefixes.some(prefix => 
          lowerKey === prefix || lowerKey.startsWith(prefix + "_") || lowerKey.endsWith("_" + prefix)
        );
        
        // Keep parameters that don't match our sensitive patterns
        if (!isSensitive && key !== "PHPSESSID") {
          newParams.append(key, value);
        }
      });
      
      // Replace the search part of the URL
      urlObj.search = newParams.toString();
      return urlObj.toString();
    } catch (e) {
      // If URL parsing fails, return the original URL
      console.warn("Failed to sanitize URL:", e);
      return url;
    }
  }