<script lang="ts">
  import Bug from "./icons/Bug.svelte";
  import Cross from "./icons/Cross.svelte";
  import { JsonView } from "@zerodevx/svelte-json-view";
  import * as states from "./states";
  import { onMount, onDestroy } from "svelte";
  import Debug from "./plugins/wp/Debug.svelte";
  import { ChevronUp, ChevronDown, Wordpress } from "$lib/icons";
  import Cors from "./debug/CORS.svelte";
    import Mouse from "./debug/Mouse.svelte";

  let isCollapsed = $state(true);
  let width = $state(15); // Width in rem
  
  const MIN_WIDTH = 15;
  const MAX_WIDTH = 50;
  const REM_TO_PX = 16;
  let isDragging = $state(false);
  let startX = 0;
  let startWidth = 0;
  let debugContainer: HTMLDivElement;
  let debugElement: HTMLElement | null = null;
  let styleElement: HTMLStyleElement | null = null;
  let sidebarElement: HTMLElement | null = $state(null);

  let modulesCollapsed = $state({
    core: false,
    wp: false,
    cors: false,
    layout: false,
  });

  function showDebug() {
    isCollapsed = false;
  }

  function hideDebug() {
    isCollapsed = true;
  }

  function toggleDebug() {
    states.settingsState.debug = !states.settingsState.debug;
  }

  function startResize(e: MouseEvent) {
    isDragging = true;
    startX = e.clientX;
    startWidth = width;
    document.addEventListener("mousemove", handleMouseMove);
    document.addEventListener("mouseup", stopResize);
    document.body.style.cursor = "ew-resize";
    document.body.style.userSelect = "none";
  }

  function handleMouseMove(e: MouseEvent) {
    if (!isDragging) return;
    const delta = e.clientX - startX;
    const deltaRem = delta / REM_TO_PX;

    // TODO: Still grows
    const sidebarRect = sidebarElement!.getBoundingClientRect();
    const parentContainer = sidebarElement!.parentElement;
    const containerWidth = parentContainer!.clientWidth || document.documentElement.clientWidth;
    const newWidth = startWidth + deltaRem;
    const maxSafeWidth = (containerWidth - sidebarRect.left + sidebarRect.width) / REM_TO_PX;
    width = Math.max(MIN_WIDTH, Math.min(MAX_WIDTH, newWidth, maxSafeWidth));
  }

  function stopResize() {
    isDragging = false;
    document.removeEventListener("mousemove", handleMouseMove);
    document.removeEventListener("mouseup", stopResize);
    document.body.style.cursor = "";
    document.body.style.userSelect = "";
  }

  function handleKeyDown(event: KeyboardEvent) {
    // Check for Ctrl+Shift+. using event.code (modern approach)
    if (event.ctrlKey && event.shiftKey && event.code === "Period") {
      showDebug();
    }

    // Fallback for older browsers
    if (
      event.ctrlKey &&
      event.shiftKey &&
      (event.key === "." || event.keyCode === 190)
    ) {
      showDebug();
    }
  }

  // Function to inject global styles for the debug panel
  function injectGlobalStyles() {
    if (typeof document === "undefined") return; // SSR check

    // Create a style element if it doesn't exist
    if (!styleElement) {
      styleElement = document.createElement("style");
      styleElement.id = "akin-debug-styles";
      document.head.appendChild(styleElement);

      // Define the styles that will apply to the debug panel in the document body
      styleElement.textContent = `
        /** body is needed when the web component is not injected into the body */
        body {
          display: flex;
          flex-direction: row;
          align-items: start; 
        }
          
        #akin-debug-portal {
          display: flex;
          align-self: stretch;
        }
        #akin-debug-portal .debug {
          position: relative;
          font-size: 0.75rem;
          margin: 0rem;
          color: black;
          min-height: 100vh;
          background-color: white;
          border-radius: 0;
          border-right: 1px solid var(--color-grey-300);
          font-family: var(--font-mono);
          overflow-y: hidden;
          transition: width 0.05s ease;
          display: flex;
          flex-direction: column;
          align-items: stretch;
        }
        #akin-debug-portal .resize-handle {
          position: absolute;
          top: 0;
          right: 0;
          width: 6px;
          height: 100%;
          cursor: ew-resize;
          z-index: 1001;
        }
        #akin-debug-portal .bug-button {
          display: none;
        }
        #akin-debug-portal button {
          background-color: transparent;
          border: none;
          border-radius: 0.5rem;
          height: 1.5rem;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        #akin-debug-portal button:hover {
          background-color: var(--color-grey-200);
        }
        #akin-debug-portal .active {
          color: var(--clr-info-a0);
          background-color: var(--color-grey-200);
        }
        #akin-debug-portal header.debug-header {
          display: flex;
          justify-content: space-between;
          gap: 0.2rem;
          align-items: center;
          padding: 0.5rem;
          border-bottom: 1px solid var(--color-grey-300);
        }
        #akin-debug-portal h3 {
          font-size: 0.75rem;
          flex: 1;
          margin: 0;
          font-weight: 400;
        }
        #akin-debug-portal .content {
          overflow-y: auto;
        }
        
        /** Module */
        #akin-debug-portal .module {
        }
        #akin-debug-portal .module-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0.5rem;
          border-bottom: 1px solid var(--color-grey-300);
        }
        #akin-debug-portal .module-header h4 {
          font-size: 0.75rem;
          flex: 1;
          margin: 0;
          font-weight: 400;
          display: flex;
          gap: 0.5rem;
        }

        #akin-debug-portal .module-content {
          padding: 0.5rem 1rem;
          overflow-y: auto;
          align-items: center;
          border-bottom: 1px solid var(--color-grey-300);
        }
      `;
    }
  }

  onMount(() => {
    window.addEventListener("keydown", handleKeyDown);

    // Create a container for our debug panel in the document body
    debugElement = document.createElement("aside");
    debugElement.id = "akin-debug-portal";
    document.body.prepend(debugElement);

    // Inject global styles
    injectGlobalStyles();

    // Move our debug panel into this container
    if (debugContainer && debugElement) {
      debugElement.prepend(debugContainer);
    }
  });

  onDestroy(() => {
    window.removeEventListener("keydown", handleKeyDown);

    // Clean up the debug element from the DOM when component is destroyed
    if (debugElement && document.body.contains(debugElement)) {
      document.body.removeChild(debugElement);
    }

    // Remove the injected styles
    if (styleElement && document.head.contains(styleElement)) {
      document.head.removeChild(styleElement);
    }
  });

  const stateObjects = $derived(
    Object.entries(states).filter(([key, value]) => {
      return (
        typeof value === "object" &&
        value !== null &&
        !Array.isArray(value) &&
        key.endsWith("State")
      );
    }),
  );

  const statesObject = $derived(
    stateObjects.reduce(
      (acc, [key, value]) => {
        acc[key.replace("State", "")] = value;
        return acc;
      },
      {} as Record<string, any>,
    ),
  );
</script>

<!-- This div will be moved to the body -->
<div
  bind:this={debugContainer}
  style="display: flex; align-self: stretch; align-items: stretch;"
>
  {#if !isCollapsed}
    <div class="debug" style="width: {width}rem;" bind:this={sidebarElement}>
      <div
        class="resize-handle"
        onmousedown={startResize}
        role="button"
        aria-label="Resize debug panel"
        tabindex="0"
      ></div>
      <header class="debug-header">
        <h3>Akin Debug</h3>
        <button
          type="button"
          aria-label="Toggle debug state"
          tabindex="0"
          onclick={toggleDebug}
          class={states.settingsState.debug ? "active" : ""}
        >
          <Bug size="1rem" />
        </button>
        <button type="button" aria-label="Hide debug panel" onclick={hideDebug}>
          <Cross width="1rem" height="1rem" />
        </button>
      </header>
      <div class="content">
        <div class="modules">
          <section class="module">
            <header class="module-header" data-module="core">
              <h4>core</h4>
              <button
                type="button"
                aria-label="Collapse module"
                onclick={() => (modulesCollapsed.core = !modulesCollapsed.core)}
              >
                {#if modulesCollapsed.core}
                  <ChevronDown size="1rem" />
                {:else}
                  <ChevronUp size="1rem" />
                {/if}
              </button>
            </header>
            {#if !modulesCollapsed.core}
              <div class="module-content">
                <div class="json">
                  <JsonView json={statesObject} depth={0} />
                </div>
              </div>
            {/if}
          </section>
          <section class="module">
            <header class="module-header" data-module="wp">
              <h4><Wordpress size="1rem" /> wp-akin</h4>
              <button
                type="button"
                aria-label="Collapse module"
                onclick={() => (modulesCollapsed.wp = !modulesCollapsed.wp)}
              >
                {#if modulesCollapsed.wp}
                  <ChevronDown size="1rem" />
                {:else}
                  <ChevronUp size="1rem" />
                {/if}
              </button>
            </header>
            {#if !modulesCollapsed.wp}
              <div class="module-content">
                <Debug />
              </div>
            {/if}
          </section>
          <section class="module">
            <header class="module-header" data-module="cors">
              <h4>CORS tests</h4>
              <button
                type="button"
                aria-label="Collapse module"
                onclick={() => (modulesCollapsed.cors = !modulesCollapsed.cors)}
              >
                {#if modulesCollapsed.cors}
                  <ChevronDown size="1rem" />
                {:else}
                  <ChevronUp size="1rem" />
                {/if}
              </button>
            </header>
            {#if !modulesCollapsed.cors}
              <div class="module-content">
                <Cors />
              </div>
            {/if}
          </section>
          <section class="module">
            <header class="module-header" data-module="cors">
              <h4>Layout</h4>
              <button
                type="button"
                aria-label="Collapse module"
                onclick={() => (modulesCollapsed.layout = !modulesCollapsed.layout)}
              >
                {#if modulesCollapsed.layout}
                  <ChevronDown size="1rem" />
                {:else}
                  <ChevronUp size="1rem" />
                {/if}
              </button>
            </header>
            {#if !modulesCollapsed.layout}
              <div class="module-content">
                <Mouse />
              </div>
            {/if}
          </section>
        </div>
      </div>
    </div>
  {/if}
</div>

<!-- 
  We're not using these styles directly since they would be scoped to this component.
  Instead, we're injecting similar styles into the document head via JavaScript.
  Keeping this here for reference.
-->
<style>
  /* These styles are for reference only - actual styles are injected via JavaScript */
</style>
