<script lang="ts">
  import { annotationsState, setHighlightedAnnotationId, loadAnnotations } from "./states";
  import AnnotationItem from "./AnnotationItem.svelte";
  import Menu from "./common/Menu.svelte";
  import TextButton from "./common/TextButton.svelte";

  // Filter states - ensure they start as 'all'
  let privacyFilter = $state<'all' | 'public' | 'private'>('all');
  let contentFilter = $state<'all' | 'annotations' | 'highlights'>('all');

  function handleHighlight(id: string) {
    setHighlightedAnnotationId(id);
    // In a real implementation, this would scroll to the highlighted text in the document
  }

  // Filtered annotations based on current filters
  const filteredAnnotations = $derived.by(() => {
    let filtered = annotationsState.annotations;
    
    console.log('=== FILTER DEBUG ===');
    console.log('Total annotations:', filtered.length);
    console.log('Privacy filter:', privacyFilter);
    console.log('Content filter:', contentFilter);
    
    // Debug: log first annotation structure
    if (filtered.length > 0) {
      console.log('Sample annotation:', {
        id: filtered[0].id,
        privacy: filtered[0].privacy,
        text: filtered[0].text,
        hasText: !!filtered[0].text
      });
    }

    // Apply privacy filter
    if (privacyFilter !== 'all') {
      const beforeCount = filtered.length;
      filtered = filtered.filter(annotation => annotation.privacy === privacyFilter);
      console.log(`Privacy filter (${privacyFilter}): ${beforeCount} → ${filtered.length}`);
    }

    // Apply content type filter
    if (contentFilter === 'highlights') {
      const beforeCount = filtered.length;
      // Highlights are annotations with no text or empty text
      filtered = filtered.filter(annotation => 
        !annotation.text || 
        annotation.text === null || 
        annotation.text.trim() === ''
      );
      console.log(`Highlights filter: ${beforeCount} → ${filtered.length}`);
    } else if (contentFilter === 'annotations') {
      const beforeCount = filtered.length;
      // Annotations have actual text content
      filtered = filtered.filter(annotation => 
        annotation.text && 
        annotation.text !== null && 
        annotation.text.trim() !== ''
      );
      console.log(`Annotations filter: ${beforeCount} → ${filtered.length}`);
    }

    console.log('Final filtered count:', filtered.length);
    return filtered;
  });

  const totalPages = $derived(
    Math.ceil((annotationsState.pagination?.total || 0) / (annotationsState.pagination?.pageSize || 100))
  )

  function loadMore() {
    loadAnnotations({
      page: annotationsState.pagination?.page || 1 + 1,
      pageSize: annotationsState.pagination?.pageSize || 100,
    });
  }

  function setPrivacyFilter(filter: 'all' | 'public' | 'private'): void {
    privacyFilter = filter;
  }

  function setContentFilter(filter: 'all' | 'annotations' | 'highlights'): void {
    contentFilter = filter;
  }
</script>

{#if !annotationsState.currentAnnotationId}
  <div class="list">
    {#if annotationsState.annotations.length === 0}
      <div class="no-annotations">
        <p>No annotations found</p>
      </div>
    {:else}
      <header>
        <div class="header-content">
          <div class="title">
            {#if privacyFilter === 'all' && contentFilter === 'all'}
              Annotations ({filteredAnnotations.length})
            {:else}
              {#if privacyFilter !== 'all' && contentFilter !== 'all'}
                {privacyFilter.charAt(0).toUpperCase() + privacyFilter.slice(1)} {contentFilter.charAt(0).toUpperCase() + contentFilter.slice(1)} ({filteredAnnotations.length})
              {:else if privacyFilter !== 'all'}
                {privacyFilter.charAt(0).toUpperCase() + privacyFilter.slice(1)} Annotations ({filteredAnnotations.length})
              {:else}
                {contentFilter.charAt(0).toUpperCase() + contentFilter.slice(1)} ({filteredAnnotations.length})
              {/if}
            {/if}
            <small>Page {annotationsState.pagination?.page || 0} of {totalPages}</small>
          </div>
          <div class="filters">
            <Menu label="Filter annotations">
              <div class="filter-section">
                <h4>Privacy</h4>
                <div class="filter-options">
                  <TextButton 
                    onclick={() => setPrivacyFilter('all')}
                    size="xs"
                    variant={privacyFilter === 'all' ? 'primary' : 'ghost'}
                  >
                    All
                  </TextButton>
                  <TextButton 
                    onclick={() => setPrivacyFilter('public')}
                    size="xs"
                    variant={privacyFilter === 'public' ? 'primary' : 'ghost'}
                  >
                    Public
                  </TextButton>
                  <TextButton 
                    onclick={() => setPrivacyFilter('private')}
                    size="xs"
                    variant={privacyFilter === 'private' ? 'primary' : 'ghost'}
                  >
                    Private
                  </TextButton>
                </div>
              </div>
              
              <div class="filter-section">
                <h4>Content Type</h4>
                <div class="filter-options">
                  <TextButton 
                    onclick={() => setContentFilter('all')}
                    size="xs"
                    variant={contentFilter === 'all' ? 'primary' : 'ghost'}
                  >
                    All
                  </TextButton>
                  <TextButton 
                    onclick={() => setContentFilter('annotations')}
                    size="xs"
                    variant={contentFilter === 'annotations' ? 'primary' : 'ghost'}
                  >
                    Annotations
                  </TextButton>
                  <TextButton 
                    onclick={() => setContentFilter('highlights')}
                    size="xs"
                    variant={contentFilter === 'highlights' ? 'primary' : 'ghost'}
                  >
                    Highlights
                  </TextButton>
                </div>
              </div>
            </Menu>
          </div>
        </div>
      </header>

      {#each filteredAnnotations as annotation (annotation.id)}
        <AnnotationItem {annotation} />
      {/each}
      
      {#if filteredAnnotations.length === 0}
        <div class="no-results">
          <p>No annotations match the current filters</p>
        </div>
      {/if}
      
      {#if totalPages > 1}
        <button onclick={loadMore}>load more</button>
      {/if}
    {/if}
  </div>
{/if}

