<script lang="ts">
  import { instanceState, settingsState, setPublicKey, setServerUrl } from "../../states"; // linking plugin to main app state
  import { loadWordpress } from "./state.svelte";
  import { api } from "../../api";
  import { onMount } from "svelte";

  // Initialize WordPress configuration on mount
  onMount(() => {
    // If WordPress config is available, set the values in the main application settings
    if (typeof window !== 'undefined' && window.akinConfig) {
      console.log("WordPress configuration found:", window.akinConfig);
      
      // Set server URL if available
      if (window.akinConfig.serverUrl && !settingsState.serverUrl) {
        console.log("Setting server URL from WordPress config:", window.akinConfig.serverUrl);
        setServerUrl(window.akinConfig.serverUrl);
        api.setServerUrl(window.akinConfig.serverUrl);
      }
      
      // Set public key if available
      if (window.akinConfig.publicKey && !settingsState.publicKey) {
        console.log("Setting public key from WordPress config:", window.akinConfig.publicKey);
        setPublicKey(window.akinConfig.publicKey);
      }
    }
  });

  // watch instance changes and load associated wordpress (auth of current user mostly)
  // TODO finalize modularization of wp
  // needs to split responsibilities in Auth.svelte
  // mostly done
  $effect(() => {
    if (instanceState.instance?.require_wp_user) {
      console.log("Instance requires WordPress user");
      if (instanceState.instance?.root_url) {
        console.log(
          "Initializing WordPress service with URL:",
          instanceState.instance.root_url,
        );
        loadWordpress(instanceState.instance.root_url);
      } else {
        console.warn("No instance URL found");
      }
    } else {
      console.log("Instance does not require WordPress user");
    }
  });
</script>