<script lang="ts">
  import Notification from "./common/Notification.svelte";
  import {
    authState,
    settingsState,
    instanceState,
    annotationsState,
    documentState,
    addDocument,
    addAnnotation,
    setDraftAnnotation,
    setCurrentAnnotationId,
  } from "./states";
  import { push } from "svelte-spa-router";
  import { getErrorMessage } from "./utils/errors";
  import { getDocumentTitle } from "./utils/meta";
    import Button from "./common/Button.svelte";

  let text = $state("");
  let dirty = $state(false);

  const loading = $derived(annotationsState.loading || documentState.loading);

  $effect(() => {
    if (text.length > 0) {
      dirty = true;
    } else {
      dirty = false;
    }
    $inspect(documentState.error);
    $inspect(annotationsState.error);
  });

  const cancel = () => {
    reset();
    push("/");
  };

  const reset = () => {
    text = "";
    dirty = false;
  };

  async function handleSubmit(event: Event) {
    event.preventDefault();

    // If document does not exist, create it
    if (!annotationsState.draft?.document_id) {
      if (!instanceState.instance?.id) {
        console.error("No instance id");
        return;
      }
      const addedDocument = await addDocument({
        url: settingsState.pageUrl,
        instance_id: instanceState.instance?.id,
        title: getDocumentTitle(),
      });
      if (!addedDocument) {
        console.error("No document added");
        return;
      }
      annotationsState.draft!.document_id = addedDocument.id;
    }

    const annotation = await addAnnotation({
      ...annotationsState.draft!,
      text,
    });
    setCurrentAnnotationId(annotation?.id!);
    reset();
  }

  function acknowledgeDocumentError() {
    documentState.error = null;
  }
  function acknowledgeAnnotationError() {
    annotationsState.error = null;
  }
</script>

{#if annotationsState.draft}
  <form onsubmit={handleSubmit}>
    {#if annotationsState.draft.quote}
      <div class="quote">
        <span>{annotationsState.draft.quote}</span>
      </div>
    {/if}

    <div class="meta">
      <div class="left">
        <div class="image">
          <img
            alt="User Avatar"
            src="https://i.pravatar.cc/32?u={authState.user?.full_name}"
          />
        </div>
        <div class="info">
          <span class="name">{authState.user?.full_name || "Anonymous"}</span>
        </div>
      </div>
    </div>
    <div class="field">
      <textarea
        id="annotation"
        class="textarea"
        name="text"
        bind:value={text}
        placeholder="What do you think?"
      ></textarea>
    </div>
    <div class="actions">
      <Button
        type="button"
        onclick={cancel}
        aria-live="polite"><span>Cancel</span></Button
      >
      <Button
        type="submit"
        variant="primary"
        aria-live="polite"
        disabled={loading || !dirty}
        ><span>{loading ? "Loading" : "Respond"}</span></Button
      >
    </div>
    {#if documentState.error && settingsState.debug}
      <div class="reminder">
        <Notification title={documentState.error.code}>
          <svelte:fragment slot="content">
            {getErrorMessage(documentState.error.code)}
          </svelte:fragment>
          <svelte:fragment slot="actions">
            <Button
              type="button"
              onclick={acknowledgeDocumentError}
              aria-live="polite"><span>OK</span></Button
            >
          </svelte:fragment>
        </Notification>
      </div>
    {/if}
    {#if annotationsState.error}
      <div class="error">
        <Notification type="error" title={annotationsState.error.code}>
          <svelte:fragment slot="content">
            {getErrorMessage(annotationsState.error.code)}
          </svelte:fragment>
          <svelte:fragment slot="actions">
            <Button
              type="button"
              onclick={acknowledgeAnnotationError}
              aria-live="polite"><span>OK</span></Button
            >
          </svelte:fragment>
        </Notification>
      </div>
    {/if}
  </form>
{/if}

