    box-sizing: border-box;
    margin-bottom: 0.5rem;
    font-family: var(--font-serif);
    font-size: 1rem;
    line-height: 1.3;
    border-radius: 1rem;
    border: none;
    outline: none;
    resize: none;
    background-color: var(--color-primary-100);
    color: var(--text-color);
  }
  .textarea:focus {
    background-color: var(--color-primary-200);
  }

  .actions {
    display: flex;
