<script lang="ts">
    import { setCurrentIndicatorY, uiState } from "../states";
    import TextButton from "../common/TextButton.svelte";
    import { push } from "svelte-spa-router";

    const currentIndicatorAnnotations = $derived(
        uiState.indicators.find((i) => i.y === parseInt(params.y!))?.annotations || [],
    );

    // Define interface for route parameters
    interface RouteParams {
        y?: string;
    }

    // Use $props() for component props in runes mode
    const { params = {} as RouteParams } = $props();

    // Use $effect to react to changes in params
    $effect(() => {
        if (params && params.y) {
            console.log("Current indicator Y from URL parameter:", params.y);
        }
    });

    $effect(() => {
        $inspect(currentIndicatorAnnotations);
        const indicator = uiState.indicators.find((i) => i.y === parseInt(params.y!));
        if (indicator) {
            setCurrentIndicatorY(indicator.y);
        }
        console.log("Current indicator Y:", params.y);
    });
</script>

<div class="container">
    <div class="cluster" style={`top: ${params.y}px; left: 0;`}>
        {#each currentIndicatorAnnotations as annotation}
            <div class="annotation-preview">
                <TextButton size="xs" tabindex="0" onclick={() => push(`/annotation/${annotation.id}`)}>
                    {annotation.quote}
                </TextButton>
                <span class="label">
                    {annotation.profile?.full_name || "Anonymous"}
                </span>
            </div>
        {/each}
    </div>
</div>

<style>
    .container {
        position: relative;
    }
    .cluster {
        position: absolute;
    }
    .annotation-preview {
        margin-bottom: 1rem;
    }
</style>
