/**
 * Central configuration for Akin web component
 * 
 * This configuration loades settings made in /wordpress/wp-content/plugins/wp-akin/ 
 * and is converted to be used in the plugins scope @akin/plugins/wp 
 */
declare global {
    interface Window {
        akinConfig: AkinWordpressConfig;
    }
}
export interface AkinWordpressConfig {
    serverUrl: string;
    pluginApiUrl: string;
    publicKey: string | null;
    /** @deprecated user is loaded through api call */
    loggedIn: string; // "1" or "0"
    /** @deprecated user is loaded through api call */
    userId: string; // "1", "2"
    /** @deprecated currently not using nounce authorization for custom api (0.0.17) */
    nonce: string | null;
}

/**
 * Configuration 
 **/

/** 
 * Server URL 
 * 
 * Hierachy:
 * - PUBLIC_WORDPRESS_SERVER_URL (run-time via js injection)
 * - fallback to ak-in attributes 
 * - fallback to PUBLIC_SERVER_URL (build-time via .env)
 * - fallback to localhost:5173 (development)
 * - fallback to https://akin.pub (production)
 */
export const PUBLIC_WORDPRESS_SERVER_URL = (window as unknown as Window).akinConfig?.serverUrl;

/**
 * Plugin API URL 
 * 
 */
export const PUBLIC_WORDPRESS_PLUGIN_API_URL = (window as unknown as Window).akinConfig?.pluginApiUrl;

/**
 * Public key
 * 
 */
export const PUBLIC_WORDPRESS_PUBLIC_KEY = (window as unknown as Window).akinConfig?.publicKey || null;

/**
 * Logged in status
 * 
 * @deprecated user is loaded through api call
 */
export const PUBLIC_WORDPRESS_LOGGED_IN = (window as unknown as Window).akinConfig?.loggedIn || '0';

/**
 * User ID
 * 
 * @deprecated user is loaded through api call
 */
export const PUBLIC_WORDPRESS_USER_ID = (window as unknown as Window).akinConfig?.userId || '0';

/**
 * Nonce
 * 
 * @deprecated currently not using nounce authorization for custom api (0.0.17)
 */
export const PUBLIC_WORDPRESS_NONCE = (window as unknown as Window).akinConfig?.nonce || null;

export const config: AkinWordpressConfig = {
    serverUrl: PUBLIC_WORDPRESS_SERVER_URL,
    pluginApiUrl: PUBLIC_WORDPRESS_PLUGIN_API_URL,
    publicKey: PUBLIC_WORDPRESS_PUBLIC_KEY,
    loggedIn: PUBLIC_WORDPRESS_LOGGED_IN,
    userId: PUBLIC_WORDPRESS_USER_ID,
    nonce: PUBLIC_WORDPRESS_NONCE
}

// For debugging
if (typeof window !== 'undefined' && window.location.hostname === 'localhost') {
    if(!config.serverUrl || !config.pluginApiUrl || !config.publicKey) {
        console.warn('Akin wordpress configuration is not set. Assuming plugin is used on non-wordpress site and manually configured');
    } else {
        console.log('Akin wordpress configuration:', config);
    }
}
