<script lang="ts">
  import {
    annotationsState,
    authState,
    uiState,
    type Indicator,
  } from "./states";
  import type { Annotation } from "./api";
  import Comment from "./icons/Comment.svelte";
  import CommentCluster from "./icons/CommentCluster.svelte";
  import TextButton from "./common/TextButton.svelte";
  import { push } from "svelte-spa-router";

  const { indicator } = $props<{ indicator: Indicator }>();

  function handleOpenDetails() {
    // TODO decide either to open the unfolding menu or load all annotations in the background
    if (indicator.annotations.length > 1) {
      push(`/indicator/${indicator.y}`);
    } else {
      push(`/annotation/${indicator.annotations[0].id}`);
    }
  }

  function handleMouseOver() {
    // TODO unfold underlying annotations
  }

  const isHovered = $derived(
    indicator.annotations.some(
      (annotation: Annotation) =>
        annotation.id === annotationsState.hoveredAnnotationId,
    ),
  );
</script>

<div
  class="item {isHovered ? 'hovered' : ''}"
  style={`top: ${indicator.y}px;left: 0;`}
  aria-label={`Annotation by ${indicator.annotations[0].profile?.full_name || "Anonymous"}`}
>
  <TextButton
    size="xs"
    tabindex="0"
    onclick={handleOpenDetails}
    onmouseover={handleMouseOver}
    onkeydown={(e: KeyboardEvent) => e.key === "Enter" && handleOpenDetails()}
  >
    {#if indicator.annotations.length > 1}
      <CommentCluster />
      <span class="label">
        Show {indicator.annotations.length} annotations
      </span>
    {:else}
      <Comment />
      <span class="label">
        {#if indicator.annotations[0].repliesCount}
          Show {indicator.annotations[0].repliesCount || 0} repl{indicator
            .annotations[0].repliesCount === 1
            ? "y"
            : "ies"}
        {:else if indicator.annotations[0].profile_id === authState.user?.id}
          Highlighted by yourself
        {:else}
          Highlighted by {indicator.annotations[0].profile?.full_name ||
            "Anonymous"}
        {/if}
      </span>
    {/if}
  </TextButton>
</div>

<style>
  .item {
    margin-bottom: 1rem;
    border-radius: 1rem;
    z-index: 10;
    position: absolute;
    color: var(--color-grey-900);
  }
  .item:hover,
  .item.hovered {
    color: var(--color-grey-800);
  }
  .label {
    margin-left: 0.5rem;
    transition: color 0.2s ease-in-out;
    @container sidebar (max-width: 2rem){
      display: none;
    }
  }
  
</style>
