<script lang="ts">
  import { onMount, onDestroy } from "svelte";
  import { formatTimeAgo } from "./utils/date";
  import type { Annotation } from "./api";

  export let annotation: Annotation | null = null;
  export let anchorElement: HTMLElement | null = null;
  export let visible: boolean = false;
  export let onSidebarOpen: () => void = () => {};
  export let onClose: () => void = () => {};

  let contextMenu: HTMLElement;
  let timeAgo: string = "";
  let menuPosition: "above" | "below" = "above"; // Default position

  $: if (annotation && annotation.created_at) {
    timeAgo = formatTimeAgo(new Date(annotation.created_at));
  }

  // Update the time ago every minute
  let timeInterval: number;

  function updatePosition() {
    if (!anchorElement || !contextMenu || !visible) return;

    const rect = anchorElement.getBoundingClientRect();
    const menuRect = contextMenu.getBoundingClientRect();

    // Position above the highlight by default
    let top = rect.top - menuRect.height - 10;
    const left = rect.left + rect.width / 2 - menuRect.width / 2;

    // If would be off the top of the screen, position below instead
    if (top < 10) {
      top = rect.bottom + 10;
      menuPosition = "below";
    } else {
      menuPosition = "above";
    }

    contextMenu.style.left = `${Math.max(10, left)}px`;
    contextMenu.style.top = `${Math.max(10, top)}px`;
  }

  $: if (visible && anchorElement) {
    // Use setTimeout to ensure DOM is updated
    setTimeout(updatePosition, 0);
    // Add click outside listener when visible
    setTimeout(addOutsideClickListener, 0);
  }

  function addOutsideClickListener() {
    if (visible) {
      window.addEventListener("click", handleOutsideClick);
    }
  }

  function handleOutsideClick(event: MouseEvent) {
    // Close the menu if clicking outside the menu and not on the highlight
    if (
      contextMenu &&
      !contextMenu.contains(event.target as Node) &&
      anchorElement &&
      !anchorElement.contains(event.target as Node)
    ) {
      onClose();
      window.removeEventListener("click", handleOutsideClick);
    }
  }

  onMount(() => {
    timeInterval = window.setInterval(() => {
      if (annotation && annotation.created_at) {
        timeAgo = formatTimeAgo(new Date(annotation.created_at));
      }
    }, 60000); // Update every minute

    // Handle window resize
    window.addEventListener("resize", updatePosition);

    // Hide menu on scroll
    window.addEventListener("scroll", handleScroll, true);
  });

  onDestroy(() => {
    if (timeInterval) clearInterval(timeInterval);
    window.removeEventListener("resize", updatePosition);
    window.removeEventListener("click", handleOutsideClick);
    window.removeEventListener("scroll", handleScroll, true);
  });

  function handleScroll() {
    if (visible) {
      onClose();
    }
  }

  function handleClick(event: Event) {
    event.stopPropagation(); // Prevent triggering the outside click handler
    onSidebarOpen();
  }

  function handleKeyDown(event: KeyboardEvent) {
    if (event.key === "Enter" || event.key === " ") {
      onSidebarOpen();
    }
  }
</script>

{#if visible && annotation}
  <div
    bind:this={contextMenu}
    class="context-menu"
    role="button"
    tabindex="0"
    on:click|stopPropagation
    on:keydown={handleKeyDown}
    aria-label="Annotation details"
  >
    <div class="menu-content">
      <button
        on:click|stopPropagation={handleClick}
        aria-label="Open sidebar with replies"
      >
        <span class="author">
          {#if annotation.replies}
            {annotation.replies
              ?.map((reply) => reply.profile?.full_name)
              .join(", ") || "Anonymous"} responded 
          {:else}
            {annotation.profile?.full_name || "Anonymous"} marked this 
          {/if}
        </span>
        <span class="time">{timeAgo}</span>
      </button>
    </div>
    <div class={`menu-arrow ${menuPosition}`}></div>
  </div>
{/if}

<style>
  .context-menu {
    position: fixed;
    font-size: 0.825rem;
    background-color: var(--color-grey-400);
    border-radius: 0.25rem;
    width: 18rem;
    max-width: 90vw;
    padding: 0;
    opacity: 0;
    transform: translateY(-0.5rem);
    animation: fade-in 0.2s ease forwards;
    pointer-events: auto;
  }

  @keyframes fade-in {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .menu-arrow {
    position: absolute;
    width: 1rem;
    height: 1rem;
    background-color: inherit;
    transform: rotate(45deg);
    left: calc(50% - 0.5rem);
  }

  .menu-arrow.above {
    bottom: -0.5rem;
  }

  .menu-arrow.below {
    top: -0.5rem;
    transform: rotate(-135deg);
  }

  .menu-content {
    padding: 0.5rem;
    border-radius: 0.5rem;
    background-color: inherit;
    position: relative;
    z-index: 2;
    display: flex;
    gap: 0.25rem;
    align-items: center;
    justify-content: center;
  }

  .context-menu:hover {
    background-color: var(--color-grey-300);
  }
  button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
    font-size: 0.825rem;
    font-family: var(--font-sans);
    color: var(--text-color);
  }

  @media (prefers-color-scheme: dark) {
  }
</style>
