/**
 * Main HTML anchoring implementation.
 * Adapted from Hypothesis's html anchoring system.
 */

import {
  RangeAnchor,
  TextPositionAnchor,
  TextQuoteAnchor,
  type AnchorOptions,
} from './types';
import type {
  Selector,
  TextQuoteSelector,
  TextPositionSelector,
  RangeSelector 
} from '../../types/selectors'

/**
 * Anchor a set of selectors.
 *
 * Converts a set of selectors into a document range.
 * It uses the selectors alone or in combination to establish 
 * the best anchor within the document.
 *
 * @param root - The root element of the anchoring context
 * @param selectors - The selectors to try
 * @param options - Optional parameters for anchoring
 * @returns A promise that resolves to a DOM Range
 */
export async function anchor(
  root: Element,
  selectors: Selector[],
  options: AnchorOptions = {}
): Promise<Range> {
  let position: TextPositionSelector | null = null;
  let quote: TextQuoteSelector | null = null;
  let range: RangeSelector | null = null;

  // Collect all the selectors
  for (const selector of selectors) {
    switch (selector.type) {
      case 'TextPositionSelector':
        position = selector;
        options.hint = position.start; // Use position as hint for TextQuoteAnchor
        break;
      case 'TextQuoteSelector':
        quote = selector;
        break;
      case 'RangeSelector':
        range = selector;
        break;
    }
  }

  /**
   * Verify that the quote matches the stored quote, if applicable
   */
  const maybeAssertQuote = (range: Range) => {
    if (quote?.exact && range.toString() !== quote.exact) {
      throw new Error('quote mismatch');
    } else {
      return range;
    }
  };

  // Try selectors in order, from simple to complex
  let promise: Promise<Range> = Promise.reject('unable to anchor');

  if (range) {
    promise = promise.catch(() => {
      try {
        const anchor = RangeAnchor.fromSelector(root, range);
        return Promise.resolve(maybeAssertQuote(anchor.toRange()));
      } catch (e) {
        return Promise.reject(e);
      }
    });
  }

  if (position) {
    promise = promise.catch(() => {
      try {
        const anchor = TextPositionAnchor.fromSelector(root, position);
        return Promise.resolve(maybeAssertQuote(anchor.toRange()));
      } catch (e) {
        return Promise.reject(e);
      }
    });
  }

  if (quote) {
    promise = promise.catch(() => {
      try {
        const anchor = TextQuoteAnchor.fromSelector(root, quote);
        return Promise.resolve(anchor.toRange(options));
      } catch (e) {
        return Promise.reject(e);
      }
    });
  }

  return promise;
}

/**
 * Create selectors that describe a range in a document.
 * 
 * @param root - The root element 
 * @param range - The DOM Range to describe
 * @returns Array of selectors that describe the range
 */
export function describe(root: Element, range: Range): Selector[] {
  const selectors: Selector[] = [];

  try {
    // Create RangeSelector
    const rangeAnchor = RangeAnchor.fromRange(root, range);
    selectors.push(rangeAnchor.toSelector());
  } catch (e) {
    console.warn('Failed to create RangeSelector:', e);
  }

  try {
    // Create TextPositionSelector
    const positionAnchor = TextPositionAnchor.fromRange(root, range);
    selectors.push(positionAnchor.toSelector());
  } catch (e) {
    console.warn('Failed to create TextPositionSelector:', e);
  }

  try {
    // Create TextQuoteSelector
    const quoteAnchor = TextQuoteAnchor.fromRange(root, range);
    selectors.push(quoteAnchor.toSelector());
  } catch (e) {
    console.warn('Failed to create TextQuoteSelector:', e);
  }

  return selectors;
}

/**
 * Highlight a range in the document.
 * 
 * @param range - The DOM Range to highlight
 * @param cssClass - The CSS class to apply to the highlight
 * @returns Array of highlight elements created
 */
export function highlightRange(range: Range, cssClass: string = 'akin-highlight'): HTMLElement[] {
  if (range.collapsed) {
    return [];
  }

  // Split text nodes at range boundaries and get only text nodes fully within range
  const textNodes = wholeTextNodesInRange(range);
  
  // Group adjacent text nodes to minimize highlight elements
  let textNodeSpans: Text[][] = [];
  let prevNode: Node | null = null;
  let currentSpan: Text[] | null = null;

  textNodes.forEach(node => {
    if (prevNode && prevNode.nextSibling === node) {
      currentSpan?.push(node);
    } else {
      currentSpan = [node];
      textNodeSpans.push(currentSpan);
    }
    prevNode = node;
  });

  // Create highlight elements for each span of text nodes
  const highlights: HTMLElement[] = [];
  
  textNodeSpans.forEach(nodes => {
    // Create the highlight element
    const highlight = document.createElement('span');
    highlight.className = cssClass;
    
    // Replace first node with highlight and move all nodes into it
    const parent = nodes[0].parentNode as ParentNode;
    parent.replaceChild(highlight, nodes[0]);
    nodes.forEach(node => highlight.appendChild(node));
    
    highlights.push(highlight);
  });
  
  return highlights;
}

/**
 * Check if any part of `node` lies within `range`.
 */
function isNodeInRange(range: Range, node: Node): boolean {
  try {
    const length = node.nodeValue?.length ?? node.childNodes.length;
    return (
      // Check start of node is before end of range.
      range.comparePoint(node, 0) <= 0 &&
      // Check end of node is after start of range.
      range.comparePoint(node, length) >= 0
    );
  } catch {
    // `comparePoint` may fail if the `range` and `node` do not share a common
    // ancestor or `node` is a doctype.
    return false;
  }
}

/**
 * Return text nodes which are entirely inside `range`.
 *
 * If a range starts or ends part-way through a text node, the node is split
 * and the part inside the range is returned.
 */
function wholeTextNodesInRange(range: Range): Text[] {
  if (range.collapsed) {
    // Exit early for an empty range to avoid an edge case that breaks the algorithm
    // below. Splitting a text node at the start of an empty range can leave the
    // range ending in the left part rather than the right part.
    return [];
  }

  let root = range.commonAncestorContainer as Node | null;
  if (root && root.nodeType !== Node.ELEMENT_NODE) {
    // If the common ancestor is not an element, set it to the parent element to
    // ensure that the loop below visits any text nodes generated by splitting
    // the common ancestor.
    //
    // Note that `parentElement` may be `null`.
    root = root.parentElement;
  }
  if (!root) {
    // If there is no root element then we won't be able to insert highlights,
    // so exit here.
    return [];
  }

  const textNodes = [];
  const nodeIter = root.ownerDocument!.createNodeIterator(
    root,
    NodeFilter.SHOW_TEXT // Only return `Text` nodes.
  );
  let node;
  while ((node = nodeIter.nextNode())) {
    if (!isNodeInRange(range, node)) {
      continue;
    }
    const text = node as Text;

    if (text === range.startContainer && range.startOffset > 0) {
      // Split `text` where the range starts. The split will create a new `Text`
      // node which will be in the range and will be visited in the next loop iteration.
      text.splitText(range.startOffset);
      continue;
    }

    if (text === range.endContainer && range.endOffset < text.data.length) {
      // Split `text` where the range ends, leaving it as the part in the range.
      text.splitText(range.endOffset);
    }

    textNodes.push(text);
  }

  return textNodes;
}

/**
 * Remove all highlights from a document.
 * 
 * @param root - The root element to search within
 * @param cssClass - The CSS class used for highlights
 */
export function removeHighlights(root: Element, cssClass: string = 'akin-highlight'): void {
  const highlights = root.querySelectorAll(`.${cssClass}`);
  
  highlights.forEach(highlight => {
    const parent = highlight.parentNode;
    if (!parent) return;
    
    // Move all children (text) outside the highlight
    while (highlight.firstChild) {
      parent.insertBefore(highlight.firstChild, highlight);
    }
    
    // Remove the empty highlight
    parent.removeChild(highlight);
  });
}

/**
 * Gets the Y position from a list of HTMLElements, accounting for scrolling
 * 
 * @param elements - The elements to get the Y position from
 * @param rootElement - The root element to use for the Y position calculation
 * @returns The Y position in pixels relative to the document
 */
export function getYPositionFromElements(elements: HTMLElement[], rootElement: Element): number {
 
  // Sort highlights by their vertical position
  elements.sort((a, b) => {
    const rectA = a.getBoundingClientRect();
    const rectB = b.getBoundingClientRect();
    return rectA.top - rectB.top;
  });

  // Get the topmost highlight
  const topmostHighlight = elements[0];
  const rect = topmostHighlight.getBoundingClientRect();
  
  const rootRect = rootElement.getBoundingClientRect();

  // Calculate the offset from the top of the document
  const yPosition = rect.top - rootRect.top;
  
  return yPosition;
}