<script lang="ts">
  import Toast from "./Toast.svelte";
  import { toastsState } from "$lib/@akin/states";

  let toastPortalTarget: HTMLElement | null = null;

  function setPortalTarget(node: HTMLElement) {
    toastPortalTarget = node;
    return {
      destroy() {
        toastPortalTarget = null;
      },
    };
  }
</script>

<div use:setPortalTarget id="toasts-outlet"></div>

{#if toastsState.toasts.length > 0}
  <section>
    {#each toastsState.toasts as toast (toast.id)}
      <Toast
        type={toast.type}
        dismissible={toast.dismissible}
        id={toast.id}
        >{toast.message}</Toast
      >
    {/each}
  </section>
{/if}

<style lang="postcss">
  section {
    position: fixed;
    bottom: 6rem;
    right: 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 1000;
  }
</style>
