import type { ApiError } from "./../api";

// Helps to find related errors by narrowing down the scope of reference search from the original error code to a scope specific one
// Example: 23505 -> Scope subscriber -> AKIN15001
export type ApiErrorContext = {
    scope: string;
    table: string;
};

export const apiErrorContextScope = {
    'general': '0',
    'api': '1',
    'database': '2',
    'server': '3',
    'client': '4',
}

export const apiErrorContextTable = {
    'general': '0',
    'instance': '1',
    'document': '2',
    'annotation': '3',
    'reply': '4',
    'subscriber': '5',
    'auth': '6',
}

// API Errors
// Akin Error Naming Conventions
// AKIN... 
// [0] = 1st digit = Scope (0 - general, 1 - api, 2 - database, 3 - server, 4 - client, etc.)
// [1] = 2nd digit = Table (0 - general, 1 - instance, 2 - document, 3 - annotation, 4 - reply, 5 - subscriber, etc.)
// [2-4] = 3rd through 5th digit = Error Number

