import type { WPAkinUser } from "./types";

interface AuthenticatedWPUser {
  isLoggedIn: boolean;
  userId?: number;
  error?: any;
  user: WPAkinUser;
}

export class WPService {
  private api_url: string;

  constructor(api_url: string) {
    this.api_url = api_url + '/wp-json/akin/v1';
  }

  /**
   * Get all users
   * @returns {Promise<{ users?: WPAkinUser[] | null; error?: unknown }>}
   * 
   * @deprecated not used yet. and not tested
   */
  async getUsers(): Promise<{ users?: WPAkinUser[] | null; error?: unknown }> {
    try {
      const response = await fetch(`${this.api_url}/users`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          // WordPress nonce for CSRF protection (optional but recommended)
          "X-WP-Nonce": this.getWPNonce(),
        },
        credentials: "include", // Important: includes WordPress session cookies
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const users = await response.json();
      return { users };
    } catch (error) {
      return { error };
    }
  }

  async getAuthStatus(): Promise<{ auth?: AuthenticatedWPUser, error?: unknown}> {
    try {
      const response = await fetch(`${this.api_url}/auth`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "X-WP-Nonce": this.getWPNonce(),
        },
        credentials: "include",
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const auth = await response.json();
      return { auth };
    } catch (error) {
      // TODO define errors properly
      return { error };
    }
  }

  /**
   * Get the WordPress nonce from the window object
   * @returns string
   * @deprecated is currently turned off in version 0.0.17 of wp plugin and should be accessed the config.ts file   */
  private getWPNonce(): string {
    // If you're running this from within WordPress admin or frontend,
    // you can get the nonce from a global variable or meta tag
    // This would need to be set by your plugin in the frontend
    return (window as any).akinConfig?.nonce || "";
  }
}
