<script lang="ts">
  import { type Annotation } from "./api";
  import { editAnnotation, addToast, setEditingAnnotationId } from "./states";

  import ToggleSwitch from "$lib/components/form-elements/ToggleSwitch.svelte";
  import { LockLockedIcon } from "$lib/icons";
  import EyeIcon from "$lib/icons/eye-icon.svelte";
    import Button from "./common/Button.svelte";

  const { annotation, onCancel, onSaved } = $props<{ 
    annotation: Annotation;
    onCancel?: () => void;
    onSaved?: () => void;
  }>();

  let isSubmitting = $state(false);
  let editText = $state(annotation.text || "");
  let privacy = $state(annotation.privacy);
  let privacyToggle = $state(annotation.privacy === "public"); // Set based on actual privacy state

  const togglePrivacy = (checked: boolean) => {
    privacyToggle = checked;
    privacy = checked ? "public" : "private";
  };

  async function handleSave() {
    if (!editText.trim()) {
      addToast({
        message: "Annotation text cannot be empty",
        type: "error",
        dismissible: true,
        timeout: 3000,
      });
      return;
    }

    isSubmitting = true;
    try {
      const updatedAnnotation = await editAnnotation(annotation.id!, {
        text: editText.trim(),
        privacy: privacy,
      });

      if (updatedAnnotation) {
        addToast({
          message: "Annotation updated successfully",
          type: "success",
          dismissible: true,
          timeout: 3000,
        });
        onSaved?.();
      } else {
        throw new Error("Failed to update annotation");
      }
    } catch (error) {
      console.error("Error updating annotation:", error);
      addToast({
        message: "Failed to update annotation",
        type: "error",
        dismissible: true,
        timeout: 5000,
      });
    } finally {
      isSubmitting = false;
    }
  }

  function handleCancel() {
    // Reset to original values
    editText = annotation.text || "";
    privacyToggle = annotation.privacy === 'public';
    onCancel?.();
  }

  function handleKeydown(event: KeyboardEvent) {
    if (event.key === 'Escape') {
      handleCancel();
    } else if (event.key === 'Enter' && (event.ctrlKey || event.metaKey)) {
      event.preventDefault();
      handleSave();
    }
  }

  function focusOnMount(node: HTMLElement) {
    node.focus();
    return {
      destroy() {
        // cleanup if needed
      }
    };
  }
</script>

<div class="annotation-edit {privacy}">
  <div class="edit-form">
    <textarea
      class="textarea"
      bind:value={editText}
      placeholder="Edit your annotation..."
      rows="3"
      disabled={isSubmitting}
      onkeydown={handleKeydown}
      use:focusOnMount
    ></textarea>
    
    <div class="controls">
      
      
      <div class="actions">
        <div class="attributes">
          <div class="privacy-toggle">
            <div class="icon-label" title="Private">
              <LockLockedIcon size={24} />
            </div>
            <ToggleSwitch
              id="reply-privacy-toggle"
              name="privacy"
              checked={privacyToggle}
              on:change={(e) => togglePrivacy(e.detail.checked)}
            />
            <div class="icon-label" title="Public">
              <EyeIcon size={24} />
            </div>
          </div>
        </div>
        <Button 
          type="button"
          onclick={handleCancel}
          disabled={isSubmitting}
        >
          <span>Cancel</span>
        </Button>
        <Button 
          type="button"
          variant="primary"
          onclick={handleSave}
          disabled={isSubmitting || !editText.trim()}
        >
          <span>{isSubmitting ? "Saving..." : "Save"}</span>
        </Button>
      </div>
    </div>
  </div>
  
  <div class="keyboard-hint">
    Press <kbd>Ctrl+Enter</kbd> to save, <kbd>Esc</kbd> to cancel
  </div>
</div>

