<script lang="ts">
  import { setSidebarOpen, annotationsState } from "./states";
  import Comment from "./icons/Comment.svelte";
  import { push } from "svelte-spa-router";

  function handleClick() {
    setSidebarOpen(true);
    push("/annotations");
  }

</script>

<button
  class="fab"
  onclick={handleClick}
  aria-label="Open annotations"
  ><Comment size="24"/>

  {#if annotationsState.annotations.length > 0}
    <span>{annotationsState.annotations.length}</span>
  {/if}
</button>

