<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import { describe } from "./utils/anchoring/html";
  import ContextMenuSelection from "./ContextMenuSelection.svelte";
  import { selectionState, setCurrentSelection } from "./states";

  let { rootElement } = $props<{ rootElement: Element | null }>();

  function handleSelection(event: MouseEvent | TouchEvent) {
    // Don't update selection if clicking on the context menu
    const target = event.target as HTMLElement;
    if (target.closest('.context-menu')) {
      event.stopPropagation();
      return;
    }
    
    selectionState.isSelecting = false;
    updateSelectionState();
  }

  function handleSelectionChange() {
    selectionState.isSelecting = true;
    updateSelectionState();
  }
  
  function updateSelectionState() {
    const selection = window.getSelection();
    
    if (!selection || selection.isCollapsed || !rootElement) {
      setCurrentSelection(null);
      return;
    }
    
    const range = selection.getRangeAt(0);
    
    if (!rootElement.contains(range.commonAncestorContainer)) {
      return;
    }
    
    const quote = range.toString().trim();
    if (!quote) return;
    
    const selectors = describe(rootElement, range);
    
    setCurrentSelection({
      quote,
      range: range.cloneRange(),
      selectors,
    });
  }
  

  onMount(() => {
    if (!rootElement) {
      rootElement = document.body;
    }

    document.addEventListener("selectionchange", handleSelectionChange);
    document.addEventListener("mouseup", handleSelection);
    document.addEventListener("touchend", handleSelection);
  });

  onDestroy(() => {
    document.removeEventListener("selectionchange", handleSelectionChange);
    document.removeEventListener("mouseup", handleSelection);
    document.removeEventListener("touchend", handleSelection);
  });
  
  // $effect(() => {
  //   const currentSelection = selectionState.currentSelection;
  //   $inspect("selecting", currentSelection);
  // });
</script>

<ContextMenuSelection />