<script>
    import { client } from "../api";

    async function testAuth() {
        try {
            // Use the raw client with proper options object
            const response = await client.get({
                url: "/auth/test",
            });
            console.log("Auth test response:", response);

            // Also log to see the data structure
            if (response.data) {
                console.log("Auth test data:", response.data);
            }
            if (response.error) {
                console.error("Auth test error in response:", response.error);
            }
        } catch (error) {
            console.error("Auth test error:", error);
        }
    }
</script>

<div id="cors-debug">
    <h2>Authentication Test</h2>
    <button onclick={testAuth}>Test Auth</button>
    <p>Check the browser console for results</p>
</div>

<style>
    #cors-debug > button {
        background-color: var(--color-grey-100);
        
    }
    #cors-debug > button:hover {
        color: var(--clr-info-a0);
        background-color: var(--color-grey-200);
    }
</style>
