/**
 * Utility for finding quotes in text content.
 * Adapted from Hypothesis's match-quote implementation.
 */

type Match = {
  /** Start offset of match in text */
  start: number;
  /** End offset of match in text */
  end: number;
  /** Score for the match between 0 and 1.0 */
  score: number;
};

type Context = {
  /** Expected text before the quote */
  prefix?: string;
  /** Expected text after the quote */
  suffix?: string;
  /** Expected offset of match within text */
  hint?: number;
};

/**
 * Simple exact text search
 */
function search(text: string, str: string): Array<{ start: number; end: number; errors: number }> {
  let matchPos = 0;
  const exactMatches: Array<{ start: number; end: number; errors: number }> = [];
  
  while (matchPos !== -1) {
    matchPos = text.indexOf(str, matchPos);
    if (matchPos !== -1) {
      exactMatches.push({
        start: matchPos,
        end: matchPos + str.length,
        errors: 0,
      });
      matchPos += 1;
    }
  }
  
  return exactMatches;
}

/**
 * Calculate a score for how well the string matches
 */
function textMatchScore(text: string, str: string): number {
  // Return 0 for empty strings
  if (str.length === 0 || text.length === 0) {
    return 0.0;
  }

  const matches = search(text, str);
  if (matches.length === 0) {
    return 0.0;
  }
  
  // For exact matches, score is 1.0
  return 1.0;
}

/**
 * Find the best match for `quote` in `text`.
 *
 * @param text - Document text to search
 * @param quote - String to find within `text`
 * @param context - Context in which the quote originally appeared
 * @return `null` if no match was found
 */
export function matchQuote(
  text: string,
  quote: string,
  context: Context = {},
): Match | null {
  if (quote.length === 0) {
    return null;
  }

  // Find exact matches for `quote` in `text`
  const matches = search(text, quote);

  if (matches.length === 0) {
    return null;
  }

  /**
   * Score the match based on quote similarity and context
   */
  const scoreMatch = (match: { start: number; end: number; errors: number }) => {
    const quoteWeight = 50; // Similarity of matched text to quote
    const prefixWeight = 20; // Similarity of text before matched text
    const suffixWeight = 20; // Similarity of text after matched text
    const posWeight = 2;     // Proximity to expected location

    // Perfect match for the quote itself
    const quoteScore = 1.0;

    // Check how well the context matches
    const prefixScore = context.prefix
      ? textMatchScore(
          text.slice(
            Math.max(0, match.start - (context.prefix.length || 0)),
            match.start,
          ),
          context.prefix,
        )
      : 1.0;
      
    const suffixScore = context.suffix
      ? textMatchScore(
          text.slice(match.end, match.end + (context.suffix.length || 0)),
          context.suffix,
        )
      : 1.0;

    // Score based on position hint
    let posScore = 1.0;
    if (typeof context.hint === 'number') {
      const offset = Math.abs(match.start - context.hint);
      posScore = 1.0 - Math.min(offset / text.length, 1.0);
    }

    // Calculate weighted score
    const rawScore =
      quoteWeight * quoteScore +
      prefixWeight * prefixScore +
      suffixWeight * suffixScore +
      posWeight * posScore;
      
    const maxScore = quoteWeight + prefixWeight + suffixWeight + posWeight;
    return rawScore / maxScore;
  };

  // Rank matches based on context and position
  const scoredMatches = matches.map(m => ({
    start: m.start,
    end: m.end,
    score: scoreMatch(m),
  }));

  // Sort by score (highest first)
  scoredMatches.sort((a, b) => b.score - a.score);
  
  return scoredMatches[0];
}
