<script lang="ts">
  import type { Privacy } from "./api";
  import {
    authState,
    repliesState,
    addReply,
    annotationsState,
    subscriptionState,
  } from "./states";
  import { LockLockedIcon } from "$lib/icons";
  import EyeIcon from "$lib/icons/eye-icon.svelte";
  import { push } from "svelte-spa-router";
    import Button from "./common/Button.svelte";
    import SubscriptionButton from "./SubscriptionButton.svelte";
    import ToggleSwitch from "$lib/components/form-elements/ToggleSwitch.svelte";

  const { annotationId } = $props();

  let text = $state("");
  let dirty = $state(false);
  let privacy = $state("public"); // Default to private
  let privacyToggle = $state(true); // Default: false = private (unchecked), true = public (checked)
  let textareaElement = $state<HTMLTextAreaElement | null>(null);

  $effect(() => {
    if (text.length > 0) {
      dirty = true;
    } else {
      dirty = false;
    }
  });

  const togglePrivacy = (checked: boolean) => {
    privacyToggle = checked;
    privacy = checked ? "public" : "private";
  };

  $effect(() => {
    if (
      annotationsState.currentAnnotationId &&
      annotationsState.currentAnnotationId === annotationId &&
      !dirty
    ) {
      setTimeout(() => {
        if (textareaElement) {
          textareaElement.focus();
        }
      }, 100);
    }
  });

  const cancel = () => {
    reset();
    push("/");
  };

  const reset = () => {
    text = "";
    dirty = false;
    privacy = "public";
    privacyToggle = true;
  };

  async function handleSubmit(event: Event) {
    event.preventDefault();
    const reply = await addReply({
      annotation_id: annotationId,
      text,
      profile_id: authState.user?.id ?? "",
      privacy: privacy as Privacy,
    });
    reset();
  }
</script>

{#if annotationId && annotationsState.currentAnnotationId === annotationId && !annotationsState.editingAnnotationId}
<form onsubmit={handleSubmit} class={privacy}>
  {#if authState.isAuthenticated}
    <div class="field">
      <textarea
        id="annotation"
        class="textarea"
        name="text"
          bind:value={text}
          bind:this={textareaElement}
          placeholder="What do you think?"
        ></textarea>
      </div>
      {#if dirty}
        <div class="actions">
           <div class="attributes">
            <div class="privacy-toggle">
              <div class="icon-label" title="Private">
                <LockLockedIcon size={24} />
              </div>
              <ToggleSwitch 
              id="reply-privacy-toggle"
              name="privacy"
              checked={privacyToggle}
              on:change={(e) => togglePrivacy(e.detail.checked)}
              />
              <div class="icon-label" title="Public">
                <EyeIcon size={24} />
              </div>
            </div>
          </div>
          <button
            type="button"
            class="button block"
            onclick={cancel}
            aria-live="polite"><span>Cancel</span></button
          >
          <button
            type="submit"
            class="button block"
            aria-live="polite"
            disabled={repliesState.loading || !dirty}
            ><span>{repliesState.loading ? "Loading" : "Respond"}</span></button
          >
        </div>
      {/if}
    {:else}
      {#if authState.isLoading}
        <div class="loading">
          <span>Loading...</span>
        </div>
      {:else if !authState.isAuthenticated}
        <div class="not-authenticated">
          <span>Please log in to respond</span>
          <Button type="button" variant="primary" onclick={()=>push("/login")}>Log in</Button>
        </div>
      {:else if authState.isAuthenticated && subscriptionState.subscription?.moderation === "approved"}
        <div class="not-authenticated">
          <span>Subscription required to respond</span>
          <SubscriptionButton/>
        </div>
      {/if}
    {/if}
  </form>
{/if}

<style>
  form {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 2rem;
    background-color: var(--color-primary-100);
    transition: background-color 0.7s ease;
    cursor: pointer;
    padding: 1rem;
  }
  form.private {
    background-color: var(--clr-warning-b0);
  }
  form.public {
    background-color: var(--clr-neutral-a0);
  }
  .textarea {
    padding: 1rem;
  }

  .textarea {
    padding: 1rem;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    font-family: var(--font-serif);
    font-size: 1rem;
    line-height: 1.3;
    border-radius: 0.75rem;
    border: none;
    outline: none;
    resize: none;
    background-color: var(--color-primary-100);
    color: var(--text-color);
  }
  .textarea:focus {
    background-color: var(--color-primary-200);
  }

  .actions {
    display: flex;
    gap: 0.5rem;
    margin-top: 1rem;
    justify-content: flex-end;
  }
  .attributes {
    display: flex;
    width: 100%;
    gap: 0.5rem;
  }
  .privacy-toggle {
    display: flex;
    align-items: center;
    gap: .25rem;
    border-radius: 1rem;
  }
  .privacy-toggle :global(.toggle-field) {
    margin: 0;
  }

  .icon-label {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 12px;
    color: var(--color-text-secondary, #666);
    transition: color 0.2s ease;
  }



  .not-authenticated {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
  }


  button {
    padding: 0.5rem 1rem;
    border-radius: 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    transition: background-color 0.7s ease;
    background-color: var(--color-grey-500);
    color: var(--color-grey-900);
  }
  button:hover {
    background-color: var(--color-grey-600);
  }
  button:disabled {
    background-color: var(--color-grey-400);
    cursor: not-allowed;
    color: var(--color-grey-600);
  }
</style>
