import type { Annotation } from "./../api";

export type SidebarPosition = 'right' | 'left' | 'top' | 'bottom' | 'detached';
export type ButtonPosition = 'inline' | 'right-top' | 'right-bottom' | 'left-top' | 'left-bottom';

export type Indicator = {
  y: number; // we use y as index as well
  annotations: Annotation[];
};

// Route-specific sidebar constraints
export type SidebarRoute = 'indicators' | 'indicator' | 'annotation' | 'annotations' | 'account' | 'other';

interface SidebarConstraints {
  minWidth: number;
  maxWidth: number;
  skipRange?: { from: number; to: number }; // Skip this range when dragging
  defaultWidth: number;
}

const SIDEBAR_CONSTRAINTS: Record<SidebarRoute, SidebarConstraints> = {
  indicators: {
    minWidth: 2,
    maxWidth: 50, // Will be clamped to half screen
    skipRange: { from: 2.1, to: 14.9 }, // Skip from 2.1rem to 14.9rem
    defaultWidth: 2
  },
  indicator: {
    minWidth: 2,
    maxWidth: 50, // Will be clamped to half screen
    skipRange: { from: 2.1, to: 14.9 }, // Skip from 2.1rem to 14.9rem
    defaultWidth: 2
  },
  annotation: {
    minWidth: 15,
    maxWidth: 50, // Will be clamped to half screen
    defaultWidth: 25
  },
  annotations: {
    minWidth: 15,
    maxWidth: 50, // Will be clamped to half screen
    defaultWidth: 25
  },
  account: {
    minWidth: 25,
    maxWidth: 40, // Smaller max width for account page
    defaultWidth: 30
  },
  other: {
    minWidth: 15,
    maxWidth: 50, // Will be clamped to half screen
    defaultWidth: 20
  }
};

// Function to derive route type from router location
export function getRouteTypeFromLocation(location: string): SidebarRoute {
  if (location === '/' || location.startsWith('/?')) {
    return 'indicators';
  }
  if (location.startsWith('/indicator/')) {
    return 'indicator';
  }
  if (location.startsWith('/annotation/')) {
    return 'annotation';
  }
  if (location.startsWith('/annotations')) {
    return 'annotations';
  }
  if (location.startsWith('/account')) {
    return 'account';
  }
  return 'other';
}

export let uiState = $state({
  sidebarOpen: true,
  sidebarWidth: 20, // in rem
  sidebarMinWidth: 15, // Will be dynamically set based on route
  sidebarMaxWidth: 30, // Will be dynamically calculated as half screen
  isMobile: typeof window !== 'undefined' ? window.innerWidth < 768 : false,
  indicators: [] as Indicator[],
  currentIndicatorY: null as number | null,
  // activeModals: [] as string[],
});

// Calculate max width as half of screen width
function calculateMaxScreenWidth(): number {
  if (typeof window === 'undefined') return 50;
  const halfScreenWidth = (window.innerWidth / 2) / 16; // Convert to rem
  return Math.max(halfScreenWidth, 20); // Minimum 20rem even on small screens
}

// Apply skip range logic for indicators route
function applySkipRange(targetWidth: number, route: SidebarRoute): number {
  const constraints = SIDEBAR_CONSTRAINTS[route];
  if (!constraints.skipRange) return targetWidth;
  
  const { from, to } = constraints.skipRange;
  
  // If trying to set width in skip range, snap to appropriate boundary
  if (targetWidth > from && targetWidth < to) {
    // Snap to closer boundary
    const distanceToFrom = Math.abs(targetWidth - from);
    const distanceToTo = Math.abs(targetWidth - to);
    const snappedWidth = distanceToFrom < distanceToTo ? from : to;
    console.log(`Skip range applied: ${targetWidth} -> ${snappedWidth} (route: ${route}, range: ${from}-${to})`);
    return snappedWidth;
  }
  
  return targetWidth;
}

// Apply route constraints based on current location
export function applyRouteConstraints(location: string) {
  const route = getRouteTypeFromLocation(location);
  const constraints = SIDEBAR_CONSTRAINTS[route];
  const maxScreenWidth = calculateMaxScreenWidth();
  
  // Update constraints
  uiState.sidebarMinWidth = constraints.minWidth;
  uiState.sidebarMaxWidth = Math.min(constraints.maxWidth, maxScreenWidth);
  
  // If current width doesn't fit new constraints, adjust it
  let newWidth = uiState.sidebarWidth;
  
  // Apply skip range if applicable
  newWidth = applySkipRange(newWidth, route);
  
  // Clamp to new min/max bounds
  newWidth = Math.max(constraints.minWidth, Math.min(uiState.sidebarMaxWidth, newWidth));
  
  // If width changed significantly or we're switching to indicators with small width
  const widthDifference = Math.abs(newWidth - uiState.sidebarWidth);
  if (widthDifference > 0.1 || ((route === 'indicators' || route === 'indicator') && uiState.sidebarWidth > 14.9)) {
    uiState.sidebarWidth = newWidth;
  }
}

// Enhanced setSidebarWidth with route-aware constraints
export function setSidebarWidth(width: number, currentLocation?: string) {
  const maxScreenWidth = calculateMaxScreenWidth();
  
  // If no location provided, get current route from existing constraints
  // This happens during drag operations - but we should try to avoid this
  let route: SidebarRoute;
  if (currentLocation) {
    route = getRouteTypeFromLocation(currentLocation);
    console.log(`Route detected from location '${currentLocation}': ${route}`);
  } else {
    // Improved fallback: check if current constraints match skip range routes first
    if (uiState.sidebarMinWidth === 2) {
      // Both indicators and indicator routes have minWidth of 2 and skip ranges
      // Default to 'indicators' since it's the most common case
      route = 'indicators';
    } else if (uiState.sidebarMinWidth === 25) {
      // Account route has minWidth of 25
      route = 'account';
    } else if (uiState.sidebarMinWidth === 15) {
      // Could be annotation, annotations, or other - default to 'other'
      route = 'other';
    } else {
      route = 'other';
    }
    console.log(`Route detected from fallback (minWidth: ${uiState.sidebarMinWidth}): ${route}`);
  }
  
  const currentConstraints = SIDEBAR_CONSTRAINTS[route];
  
  // Update max width to current screen size
  uiState.sidebarMaxWidth = Math.min(currentConstraints.maxWidth, maxScreenWidth);
  
  // Apply skip range logic
  let constrainedWidth = applySkipRange(width, route);
  
  // Apply min/max constraints
  constrainedWidth = Math.max(
    uiState.sidebarMinWidth,
    Math.min(uiState.sidebarMaxWidth, constrainedWidth)
  );
  
  uiState.sidebarWidth = constrainedWidth;
}

// Update max width when window resizes
export function updateMaxWidth(currentLocation?: string) {
  const maxScreenWidth = calculateMaxScreenWidth();
  
  // If location provided, apply route constraints
  if (currentLocation) {
    applyRouteConstraints(currentLocation);
  } else {
    // Just update max width while preserving current constraints
    const route = Object.keys(SIDEBAR_CONSTRAINTS).find(key => 
      SIDEBAR_CONSTRAINTS[key as SidebarRoute].minWidth === uiState.sidebarMinWidth
    ) as SidebarRoute || 'other';
    
    const currentConstraints = SIDEBAR_CONSTRAINTS[route];
    uiState.sidebarMaxWidth = Math.min(currentConstraints.maxWidth, maxScreenWidth);
    
    // Adjust current width if it exceeds new maximum
    if (uiState.sidebarWidth > uiState.sidebarMaxWidth) {
      uiState.sidebarWidth = uiState.sidebarMaxWidth;
    }
  }
}

export function addAnnotationToIndicator(annotation: Annotation, y: number) {
  const roundedY = Math.round(y);
  const indicator = uiState.indicators.find(i => i.y === roundedY);

  if (indicator) {
    if (indicator.annotations.some(a => a.id === annotation.id)) {
      return;
    }

    const updatedIndicator = {
      ...indicator,
      annotations: [...indicator.annotations, annotation]
    };

    uiState.indicators = uiState.indicators.map(ind =>
      ind.y === roundedY ? updatedIndicator : ind
    );

    return;
  }

  uiState.indicators = [...uiState.indicators, { y: roundedY, annotations: [annotation] }];
}

export function setCurrentIndicatorY(y: number) {
  uiState.currentIndicatorY = y;
}

export function resetIndicators() {
  uiState.indicators = [];
}

export function setSidebarOpen(isOpen: boolean) {
  uiState.sidebarOpen = isOpen;
}

export function toggleSidebar() {
  uiState.sidebarOpen = !uiState.sidebarOpen;
}

export function setIsMobile(isMobile: boolean) {
  uiState.isMobile = isMobile;
}

export function setSidebarMinWidth(width: number) {
  uiState.sidebarMinWidth = width;
}

export function setSidebarMaxWidth(width: number) {
  uiState.sidebarMaxWidth = width;
}
