<script lang="ts">
  import {
    login,
    authState,
    settingsState,
    instanceState,
    addToast,
  } from "./states";
  import Notification from "./common/Notification.svelte";
  import { onMount } from "svelte";
  import {
    wpState,
    loadWordpress,
    getAuthStatus,
    wpIsLoggedIn,
  } from "./plugins/wp/state.svelte";
  import Button from "./common/Button.svelte";
  import { getApiError } from "./utils/errors";
  import { Wordpress } from "$lib/icons";
  import Identity from "./Identity.svelte";

  let loading = $state(false);
  let email = $state("");
  let username = $state("");
  let redirect_url = $state("");
  let notification = $state<string | null>(null);
  let error = $state<string | null>(null);

  // Track if WordPress is available and initialized
  let wpAvailable = $state(false);

  // Computed property to determine if WordPress is required
  let requireWordPress = $derived(
    instanceState.instance?.require_wp_user ?? false,
  );

  onMount(async () => {
    try {
      // Only initialize WordPress if it's required and if the instance has a root_url
      if (requireWordPress && instanceState.instance?.root_url) {
        loadWordpress(instanceState.instance?.root_url);
        wpAvailable = true;

        const wpAuthenticated = await getAuthStatus();

        if (wpAuthenticated && wpState.wpUser) {
          username = wpState.wpUser.username;

          if (wpState.wpUser.email) {
            email = wpState.wpUser.email;
          }

          console.log("WordPress user authenticated:", wpState.wpUser);
        }
      }
    } catch (error) {
      console.error("Error initializing WordPress integration:", error);
      // Don't block the auth flow if WordPress fails
      wpAvailable = false;
    }
  });

  const handleLogin = async (e: Event) => {
    e.preventDefault();
    error = null;
    loading = true;
    try {
      // Your existing login logic
      console.log("implement login through auth state");
      // generate redirect url (self is default)
      redirect_url = `${window.location.href}`;

      // Only pass username if WordPress is required
      let success = false;
      if(requireWordPress && wpState.wpUser?.id && wpState.wpUser?.username && wpState.wpUser?.email){
        
        success = await login(
          wpState.wpUser?.email,
          settingsState.publicKey,
          wpState.wpUser?.id,
          wpState.wpUser?.username,
          redirect_url,
        );
      } else {
        // Login with email only
        success = await login(
          email,
          settingsState.publicKey,
          undefined,
          undefined,
          redirect_url,
        );
      }

      if (success) {
        // set notification and toast
        notification = "Check your email for login link!";
        addToast({
          message: "Check your email for login link!",
          type: "success",
          dismissible: true,
          timeout: 3000,
        });
      } else {
        // set error and toast
        error = "Signup failed";
        addToast({
          message:
            getApiError(authState.error?.code, {
              scope: "auth",
              table: "subscriber",
            }).message || "Login failed",
          type: "error",
          dismissible: true,
          timeout: 10000,
        });
      }
    } catch (error) {
      error =
        error instanceof Error ? error.message : "An unknown error occurred";
    } finally {
      loading = false;
    }
  };
</script>

<div class="auth-form-widget" aria-live="polite">
  <h2 class="header">Login</h2>

  <!-- WordPress authentication status indicator - only shown if WordPress is required -->
  {#if requireWordPress && wpAvailable}
    {#if wpState.wpIsLoading}
      <Notification>
        <svelte:fragment slot="content">
          <div class="notification-checking">
            <Wordpress size="3rem" loading />
            <p>Checking your priviliges...</p>
          </div>
        </svelte:fragment>
      </Notification>
    {:else if !wpState.wpIsLoggedIn}
      <Notification type="warning">
        <svelte:fragment slot="content">
          <p>Login to {instanceState.instance?.name} WordPress first.</p>
        </svelte:fragment>
      </Notification>
    {/if}
  {/if}

  <form class="auth-form" onsubmit={handleLogin} autocomplete="on">
    <!-- Username field - only shown when WordPress is required -->
    {#if requireWordPress && wpAvailable && !wpState.wpIsLoading && wpState.wpIsLoggedIn}
      {#if wpState.wpIsLoggedIn && wpState.wpUser?.username && wpState.wpUser?.email}
        <input
          id="wp_id"
          class="inputField"
          type="hidden"
          value={wpState.wpUser?.id}
        />
        <input
          id="username"
          class="inputField"
          type="hidden"
          value={wpState.wpUser?.username}
        />
        <input
          id="email"
          class="inputField"
          type="hidden"
          value={wpState.wpUser?.email}
        />

        <h3>
          Welcome {wpState.wpUser?.displayName ||
            wpState.wpUser?.firstName ||
            wpState.wpUser?.lastName ||
            wpState.wpUser?.username},
        </h3>
        <p class="description">
          To use the akin annotation service, you need to opt-in to use it.
          Please confirm your are sharing the following data with {instanceState
            .instance?.name}.
        </p>
        <Identity
          identity={{
            provider: "wp-akin",
            data: wpState.wpUser,
            host: instanceState.instance?.root_url || "",
          }}
        />
        <div class="auth-form-widget-actions">
          <Button
            variant="accent"
            type="submit"
            aria-live="polite"
            disabled={loading}
          >
            <span>{loading ? "Loading..." : "Sign in"}</span>
          </Button>
        </div>
      {/if}
    {:else if !requireWordPress && !wpState.wpIsLoading && !wpState.wpIsLoggedIn}
      <p class="description">
        We need you to sign-in to use our annotation service.
      </p>
      <div class="field {authState.error?.validationErrors?.fieldErrors?.email ? 'has-error' : ''}">
        <label for="email">Email</label>
        <input
          id="email"
          class="inputField"
          type="email"
          autocomplete="on"
          placeholder="Your email"
          bind:value={email}
        />
        {#if authState.error?.validationErrors?.fieldErrors?.email}
          <div class="field-error-message">{authState.error?.validationErrors?.fieldErrors?.email[0]}</div>
        {/if}
      </div>
      <div class="auth-form-widget-actions">
        <Button
          variant="accent"
          type="submit"
          aria-live="polite"
          disabled={loading}
        >
          <span>{loading ? "Loading..." : "Sign in"}</span>
        </Button>
      </div>
      <p class="hint">
        If you don't have an account, yet it will be created for you.
      </p>
    {/if}

    {#if authState.error && settingsState.debug}
      <Notification title="Authentication Error" type="error">
        <svelte:fragment slot="content">
          <p>
            {authState.error?.validationErrors?.message ||
              authState.error?.message}
          </p>
          {#if authState.error?.validationErrors?.fieldErrors}
            <ul class="field-errors">
              {#each Object.entries(authState.error?.validationErrors?.fieldErrors as Record<string, string[]>) as [field, errors]}
                {#each errors as errorMsg}
                  <li><strong>{field}:</strong> {errorMsg}</li>
                {/each}
              {/each}
            </ul>
          {/if}
          {#if authState.error?.validationErrors?.formErrors && authState.error?.validationErrors?.formErrors.length > 0}
            <ul class="form-errors">
              {#each authState.error?.validationErrors?.formErrors as errorMsg}
                <li>{errorMsg}</li>
              {/each}
            </ul>
          {/if}
        </svelte:fragment>
      </Notification>
    {/if}
    {#if notification}
      <Notification title="Success" type="success">
        <svelte:fragment slot="content">
          <p>{notification}</p>
        </svelte:fragment>
      </Notification>
    {/if}
    <!-- {#if error}
      <Notification title="Error" type="error">
        <svelte:fragment slot="content">
          <p>{error}</p>
        </svelte:fragment>
      </Notification>
    {/if} -->
  </form>
</div>

