    gap: 1rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }
  .description {
    color: var(--color-grey-700);
    font-size: 1.1rem;
    margin-top: 0.5rem;
    margin-bottom: 1.55rem;
  }
  .hint {
    color: var(--color-grey-800);
    font-size: 0.9rem;
    margin-top: 0.5rem;
    margin-bottom: 1.55rem;
    text-align: center;
  }

  .field.has-error input {
    border-color: var(--clr-danger-a0);
  }

  .field-error-message {
    color: var(--clr-danger-a0);
