<script lang="ts">
  import { type Reply } from "./api";
  import { editReply, addToast, setEditingReplyId } from "./states";

  import ToggleSwitch from "$lib/components/form-elements/ToggleSwitch.svelte";
  import { LockLockedIcon } from "$lib/icons";
  import EyeIcon from "$lib/icons/eye-icon.svelte";
  import Button from "./common/Button.svelte";

  const { reply, onCancel, onSaved } = $props<{
    reply: Reply;
    onCancel?: () => void;
    onSaved?: () => void;
  }>();

  let isSubmitting = $state(false);
  let editText = $state(reply.text || "");
  let privacy = $state(reply.privacy);
  let privacyToggle = $state(reply.privacy === "public"); // Set based on actual privacy state

  const togglePrivacy = (checked: boolean) => {
    privacyToggle = checked;
    privacy = checked ? "public" : "private";
  };

  async function handleSave() {
    if (!editText.trim()) {
      addToast({
        message: "Reply text cannot be empty",
        type: "error",
        dismissible: true,
        timeout: 3000,
      });
      return;
    }

    isSubmitting = true;
    try {
      const updatedReply = await editReply(reply.id!, {
        text: editText.trim(),
        privacy: privacy,
      });

      if (updatedReply) {
        addToast({
          message: "Reply updated successfully",
          type: "success",
          dismissible: true,
          timeout: 3000,
        });
        onSaved?.();
      } else {
        throw new Error("Failed to update reply");
      }
    } catch (error) {
      console.error("Error updating reply:", error);
      addToast({
        message: "Failed to update reply",
        type: "error",
        dismissible: true,
        timeout: 5000,
      });
    } finally {
      isSubmitting = false;
    }
  }

  function handleCancel() {
    // Reset to original values
    editText = reply.text || "";
    privacyToggle = reply.privacy === "public";
    setEditingReplyId(null);
    onCancel?.();
  }

  function handleKeydown(event: KeyboardEvent) {
    if (event.key === "Escape") {
      handleCancel();
    } else if (event.key === "Enter" && (event.ctrlKey || event.metaKey)) {
      event.preventDefault();
      handleSave();
    }
  }

  function focusOnMount(node: HTMLElement) {
    node.focus();
    return {
      destroy() {},
    };
  }
</script>

<div class="reply-edit {privacy}">
  <div class="edit-form">
    <div class="field">
      <textarea
        class="textarea"
        bind:value={editText}
        onkeydown={handleKeydown}
        use:focusOnMount
        placeholder="Edit your reply..."
        rows="3"
        disabled={isSubmitting}
      ></textarea>
    </div>

    <div class="controls">
      <div class="actions">
        <div class="attributes">
          <div class="privacy-toggle">
            <div class="icon-label" title="Private">
              <LockLockedIcon size={24} />
            </div>
            <ToggleSwitch
              id="reply-privacy-toggle"
              name="privacy"
              checked={privacyToggle}
              on:change={(e) => togglePrivacy(e.detail.checked)}
            />
            <div class="icon-label" title="Public">
              <EyeIcon size={24} />
            </div>
          </div>
        </div>
        <Button
          type="button"
          onclick={handleCancel}
          disabled={isSubmitting}
          class="button block"
        >
          Cancel
        </Button>
        <Button
          type="button"
          onclick={handleSave}
          aria-live="polite"
          disabled={isSubmitting || !editText.trim()}
          class="button block"
        >
          {isSubmitting ? "Saving..." : "Save"}
        </Button>
      </div>
    </div>

    <div class="keyboard-shortcuts">
      <small
        >Press <kbd>Ctrl+Enter</kbd> to save, <kbd>Esc</kbd> to cancel</small
      >
    </div>
  </div>
</div>

