// annotations.svelte.ts
import type { QueryPagination } from "$lib/types/pagination";
import { createReply, deleteReply, getReplies, updateReply, type Reply, type ReplyCreate, type ReplyUpdate } from "../api";
import { annotationsState } from "./annotations.svelte";

// Export state as object properties (reply data only)
export let repliesState = $state({
  replies: [] as Reply[],
  pagination: {
    page: 1,
    pageSize: 100,
    total: 0,
  } as QueryPagination | null,
  loading: false,
  error: null as string | null,
  editingReplyId: null as string | null,
});

/**
 * Load replies from the API
 */
export async function loadReplies(annotationId?: string, params?: {
  page?: string;
  documentId?: string;
  pageSize?: number;
}): Promise<void> {
  repliesState.loading = true;
  repliesState.error = null;

  try {
    // Use the provided annotationId parameter or fall back to the current one from state
    const targetAnnotationId = annotationId || annotationsState.currentAnnotationId;

    if (!targetAnnotationId) {
      repliesState.loading = false;
      repliesState.error = "No annotation ID available";
      return;
    }

    const page = params?.page ? parseInt(params.page) : 1;
    const pageSize = params?.pageSize || 100;

    // Using the new generated SDK directly
    const response = await getReplies({
      query: {
        annotation_id: targetAnnotationId,
        page,
        page_size: pageSize,
      }
    });

    if (response.error) {
      throw response.error;
    }

    console.log("replies state updated", { data: response.data.replies });
    repliesState.replies = response.data?.replies || [];
    repliesState.pagination = response.data?.pagination || null;
    repliesState.loading = false;
    return;
  } catch (err) {
    console.error("Error loading replies:", err);
    repliesState.loading = false;
    repliesState.error =
      err instanceof Error ? err.message : "Failed to load replies";
    repliesState.replies = [];
    return;
  }
}

/**
 * Create a new reply and if successful update state
 * @param newReply ReplyCreate
 */
export async function addReply(newReply: ReplyCreate): Promise<Reply | null | undefined> {
  repliesState.loading = true;
  repliesState.error = null;
  try {
    // Using the new generated SDK directly
    const response = await createReply({
      body: newReply
    });

    repliesState.loading = false;
    if (response.error) {
      throw response.error;
    }

    const createdReply = response.data?.reply;
    console.log("reply created", { reply: createdReply });

    if (createdReply) {
      repliesState.replies = [
        ...repliesState.replies,
        createdReply
      ];
    }

    return createdReply;
  } catch (error) {
    console.error("Error creating reply:", error);
    repliesState.loading = false;
    repliesState.error =
      error instanceof Error ? error.message : "Failed to create reply";
    return null;
  }
}

/**
 * Update a reply and if successful update state
 * @param id The reply ID to update
 * @param update The reply update
 */
export async function editReply(id: string, update: ReplyUpdate): Promise<Reply | null | undefined> {
  repliesState.loading = true;
  repliesState.error = null;
  try {
    // Using the new generated SDK directly
    // The server expects the id in the body of the request as well as the query
    // Use type assertion since AnnotationUpdate type doesn't include id field
    const response = await updateReply({
      body: { ...update, id } as any,
      query: { id }
    });

    repliesState.loading = false;
    if (response.error) {
      throw response.error;
    }

    const updatedReply = response.data?.reply;
    console.log("reply updated", { reply: updatedReply });

    if (updatedReply) {
      const replyIndex = repliesState.replies.findIndex(
        (r) => r.id === updatedReply.id
      );
      if (replyIndex !== -1) {
        const newReplies = [...repliesState.replies];
        newReplies[replyIndex] = updatedReply;
        repliesState.replies = newReplies;
      }
    }
    return updatedReply || null;
  } catch (error) {
    console.error("Error updating reply:", error);
    repliesState.loading = false;
    repliesState.error =
      error instanceof Error ? error.message : "Failed to update reply";
    return null;
  }
}

/**
 * Delete a reply and if successful update state
 * @param id The reply ID to delete
 */
export async function removeReply(id: string): Promise<boolean> {
  repliesState.loading = true;
  repliesState.error = null;
  try {
    // Using the new generated SDK directly
    const response = await deleteReply({
      query: {
        id
      }
    });

    if (response.error) {
      throw response.error;
    }

    // Remove from the replies array
    repliesState.replies = repliesState.replies.filter(
      (reply) => reply.id !== id
    );
    repliesState.loading = false;
    return true;
  } catch (err) {
    console.error("Error deleting reply:", err);
    repliesState.loading = false;
    repliesState.error =
      err instanceof Error ? err.message : "Failed to delete reply";
    return false;
  }
}

/**
 * Set the editing reply ID
 * @param replyId The reply ID to edit, or null to stop editing
 */
export function setEditingReplyId(replyId: string | null): void {
  repliesState.editingReplyId = replyId;
}
