import type { Selector } from '../types/selectors';

export let selectionState = $state({
  currentSelection: null as {
    quote: string;
    range: Range | null;
    selectors: Selector[];
  } | null,
  isSelecting: false,
});

export function setCurrentSelection(selection: {
  quote: string;
  range: Range | null;
  selectors: Selector[];
} | null) {
  selectionState.currentSelection = selection;
}

export function clearSelection() {
  selectionState.currentSelection = null;
  selectionState.isSelecting = false;
}

export function hasActiveSelection() {
  return selectionState.currentSelection !== null;
}
