import type { Selector, RangeSelector, TextPositionSelector, TextQuoteSelector } from "../../types/selectors";

export function convertToValidSelectors(dbSelectors: { [key: string]: any; type: string }[]): Selector[] {
    return dbSelectors.map(selector => {
      switch (selector.type) {
        case 'RangeSelector':
          return {
            type: 'RangeSelector',
            startContainer: selector.startContainer || '',
            endContainer: selector.endContainer || '',
            startOffset: selector.startOffset || 0,
            endOffset: selector.endOffset || 0
          } as RangeSelector;
        
        case 'TextPositionSelector':
          return {
            type: 'TextPositionSelector',
            start: selector.start || 0,
            end: selector.end || 0
          } as TextPositionSelector;
          
        case 'TextQuoteSelector':
          return {
            type: 'TextQuoteSelector',
            exact: selector.exact || '',
            prefix: selector.prefix,
            suffix: selector.suffix
          } as TextQuoteSelector;
          
        default:
          throw new Error(`Unknown selector type: ${selector.type}`);
      }
    });
  }
  
