<script lang="ts">
  import { formatTimeAgo } from "$lib/utils/date";
  import { type Reply } from "./api";
  import ReplyControls from "./ReplyItemControls.svelte";
  import ReplyEdit from "./ReplyEdit.svelte";
  import { settingsState, repliesState, setEditingReplyId } from "./states";

  const { reply } = $props<{ reply: Reply }>();

  let timeAgo = $derived(
    reply.created_at ? formatTimeAgo(new Date(reply.created_at)) : "",
  );
  
  let isEditMode = $derived(repliesState.editingReplyId === reply.id);
</script>

{#if reply}
  <div class="reply-item {reply.privacy}">
    <div class="reply-meta">
      <div class="left">
        <div class="info">
          <a href="{settingsState.serverUrl}/p/{reply.profile?.username}">
            <span class="name">{reply.profile?.full_name || "Anonymous"}</span>
          </a>
          <span class="time">{timeAgo}</span>
        </div>
      </div>
      <div class="right">
        <ReplyControls {reply} />
      </div>
    </div>
    {#if isEditMode}
      <ReplyEdit 
        {reply} 
        onCancel={() => setEditingReplyId(null)}
        onSaved={() => setEditingReplyId(null)}
      />
    {:else}
      <div class="reply-text">{reply.text}</div>
    {/if}
  </div>
{:else}
  <div class="reply-item">
    <div class="reply-text">Reply missing</div>
  </div>
{/if}

