<script lang="ts">
  import type { WPAkinUser } from "./plugins/wp/types";
  import { instanceState, subscriptionState } from "./states";
  import { wpState } from "./plugins/wp/state.svelte";
  import { Wordpress } from "$lib/icons";
  import ToggleSwitch from "$lib/components/form-elements/ToggleSwitch.svelte";
  import LoggedIn from "./icons/LoggedIn.svelte";
  import Linked from "./icons/Linked.svelte";

  const { identity } = $props<{
    identity: { provider: string; data: WPAkinUser | unknown; host: string };
  }>();

  const knownProvider = ["wp-akin", "email"];
  const mapIdentityType = (provider: string) => {
    switch (provider) {
      case "wp-akin":
        return "WordPress";
      case "email":
        return "Email";
      default:
        return "Unknown";
    }
  };

  const getIdentityData = (identity: {
    provider: string;
    data: WPAkinUser | unknown;
    host: string;
  }): WPAkinUser | unknown => {
    switch (identity.provider) {
      case "wp-akin":
        return identity.data as WPAkinUser;
      default:
        return identity.data as unknown;
    }
  };

  const identityData = $derived(getIdentityData(identity));
  let collapsed = $state(true);

  const getBaseName = (url: string) => {
    const parsedUrl = new URL(url);
    return parsedUrl.hostname;
  };
</script>

{#if identity.provider === "wp-akin"}
  <div
    class="identity {instanceState.instance?.require_wp_user &&
    instanceState.instance?.root_url === identity.host
      ? 'active'
      : 'inactive'}"
  >
    <button
      class="identity-header"
      type="button"
      onclick={() => (collapsed = !collapsed)}
    >
      <header>
        <Wordpress size="3rem" />
        <div class="identity-info">
          <h4>{mapIdentityType(identity.provider)}</h4>
          <p>{getBaseName(identity.host)}</p>
        </div>
        <div class="identity-status">
          {#if instanceState.instance?.require_wp_user && instanceState.instance?.root_url === identity.host}
            <div class="status-indicator">
              <LoggedIn
                loading={wpState.wpIsLoading}
                color={wpState.wpIsLoggedIn
                  ? "var(--clr-accent-a0)"
                  : "var(--clr-neutral-a0)"}
                size="1rem"
              />
            </div>
          {:else}
            <p>NaN</p>
          {/if}
          <div class="status-indicator">
            <Linked
              loading={subscriptionState.isLoading}
              color={subscriptionState.subscription?.moderation === "approved"
                ? "var(--clr-accent-a0)"
                : "var(--clr-neutral-a0)"}
              size="1rem"
            />
          </div>
          1
        </div>
      </header>
    </button>
    <div class="content {collapsed ? 'collapsed' : ''}">
      {#if instanceState.instance?.require_wp_user && instanceState.instance?.root_url === identity.host}
        <div class="wp-status">
          {#if wpState.wpIsLoggedIn}
            <p>Logged in to this wordpress.</p>
          {:else}
            <p>Not logged in to this wordpress.</p>
          {/if}
        </div>
      {:else}
        <div class="wp-status">Login not required.</div>
      {/if}
      <table>
        <tbody>
          <tr>
            <td>Username</td>
            <td>{(identityData as WPAkinUser)?.username}</td>
          </tr>
          <tr>
            <td>Display Name</td>
            <td>{(identityData as WPAkinUser)?.displayName}</td>
          </tr>
          <tr>
            <td>First Name</td>
            <td>{(identityData as WPAkinUser)?.firstName}</td>
          </tr>
          <tr>
            <td>Last Name</td>
            <td>{(identityData as WPAkinUser)?.lastName}</td>
          </tr>
          <tr>
            <td>Email</td>
            <td>{(identityData as WPAkinUser)?.email}</td>
          </tr>
          <tr>
            <td>Roles</td>
            <td class="role"
              >{(identityData as WPAkinUser)?.roles?.join(", ")}</td
            >
          </tr>
          {#if (identityData as WPAkinUser)?.capabilities}
            <tr>
              <td>Capabilities</td>
              <td class="role"
                >{#each Object.keys((identityData as WPAkinUser)?.capabilities) as cap}
                  <ToggleSwitch
                    id={cap}
                    label={cap}
                    name={cap}
                    checked={(identityData as WPAkinUser)?.capabilities[cap]}
                    disabled={true}
                  />
                {/each}</td
              >
            </tr>
          {/if}
          <tr>
            <td>Avatar</td>
            <td
              ><img
                src={(identityData as WPAkinUser)?.avatar}
                alt="Avatar"
              /></td
            >
          </tr>
        </tbody>
      </table>
    </div>
  </div>
{:else if !knownProvider.includes(identity.provider)}
  <div class="identity">
    <p>Unknown identity</p>
    {JSON.stringify(identity.data)}
  </div>
{/if}

<style>
  .identity {
    border: 1px solid var(--color-grey-400);
    border-radius: 0.5rem;
    background-color: var(--color-grey-100);
    transition: background-color 0.7s ease;
  }
  .identity-header {
    margin-bottom: 1rem;
    display: block;
    cursor: pointer;
    background: none;
    border: none;
    padding: 1rem;
    background: none;
    margin: 0;
    font-family: var(--font-sans);
    font-size: 1rem;
    font-weight: 400;
    color: var(--color-grey-900);
    text-align: left;
    width: 100%;
  }
  .identity:hover {
    background-color: var(--clr-neutral-a0);
  }
  .identity.active {
    border-color: var(--color-primary-400);
    background-color: var(--color-primary-100);
  }
  header {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    align-items: center;
  }
  .identity-info {
    flex: 1;
  }
  .content {
    margin-bottom: 1rem;
    padding: 0 1rem;
  }
  .content.collapsed {
    display: none;
  }
  h4 {
    font-size: 1rem;
    margin: 0;
  }
  p {
    margin: 0;
    font-size: 0.9rem;
  }
  .wp-status {
    margin: 1rem 0;
    font-size: 0.7rem;
    color: var(--color-grey-600);
    font-weight: 600;
    text-align: center;
  }
  .wp-status p {
    margin: 0rem;
  }
  .identity-status {
    display: flex;
    gap: 0.5rem;
  }
  .status-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1rem;
    height: 1rem;
  }
  table {
    width: 100%;
    border-collapse: collapse;
  }
  table tr {
    border-bottom: 1px solid var(--color-grey-400);
  }
  table tr td {
    padding: 0.5rem 0;
    font-size: 0.9rem;
  }
  table tr td:first-child {
    font-weight: 600;
    width: 6rem;
  }
  table tr:last-child {
    border-bottom: 0;
  }
  .role {
    text-transform: capitalize;
  }
</style>
