import { apiErrors, apiErrorContextScope, apiErrorContextTable, type ApiError, type ApiErrorContext } from "../types/errors";

/** 
 * Convert API code to error message
 */
export function getErrorMessage(code: string): string {
    const error = getApiError(code);
    return error.message || "An unexpected error occurred";
}

function getPrefix(context: ApiErrorContext): string {
    const prefix = `AKIN${apiErrorContextScope[context.scope as keyof typeof apiErrorContextScope]}${apiErrorContextTable[context.table as keyof typeof apiErrorContextTable]}`;
    return prefix;
}

export function getApiError(code: string, context?: ApiErrorContext): ApiError {
    if (context) {
        const prefix = getPrefix(context);
        // map error more specifically depending on context
        const contextualErrors = apiErrors.filter((error: ApiError) => error.code.startsWith(prefix));
        if (contextualErrors.length > 0){
            // 
            return contextualErrors
            .find((error: ApiError) => error.refCode === code) || contextualErrors[0];
        }
    }
    return apiErrors.find((error: ApiError) => error.code === code) || apiErrors[0];
}