// annotations.svelte.ts
import type { QueryPagination } from "$lib/types/pagination";
import {
  getAnnotations,
  createAnnotation,
  updateAnnotation,
  deleteAnnotation,
  type Annotation,
  type AnnotationCreate,
  type AnnotationUpdate,
  type ApiError
} from "../api";
import { getApiError } from "../utils/errors";
import { documentState } from "./document.svelte";

// Export state as object properties (annotation data only)
export let annotationsState = $state({
  annotations: [] as Annotation[],
  draft: null as AnnotationCreate | null,
  pagination: {
    page: 1,
    pageSize: 100,
    total: 0,
  } as QueryPagination | null,
  currentAnnotationId: null as string | null,
  hoveredAnnotationId: null as string | null,
  highlightedAnnotationId: null as string | null,
  editingAnnotationId: null as string | null,
  loading: false,
  error: null as ApiError | null,
});

/**
 * Load annotations from the API
 */
export async function loadAnnotations(params?: {
  page?: number;
  documentId?: string;
  pageSize?: number;
  includeReplies?: boolean;
}): Promise<void> {
  annotationsState.loading = true;
  annotationsState.error = null;

  try {
    const docId = params?.documentId || documentState.document?.id;

    if (!docId) {
      annotationsState.loading = false;
      annotationsState.error = getApiError("AKIN12001");
      return;
    }

    const page = params?.page ? params.page : 1;
    const pageSize = params?.pageSize || 100;
    const includeReplies = params?.includeReplies || false;

    // Using the new generated SDK directly
    const response = await getAnnotations({
      query: {
        document_id: docId,
        page,
        page_size: pageSize,
        includeReplies
      }
    });

    if (response.error) {
      throw response.error;
    }

    annotationsState.annotations = response.data?.annotations || [];
    annotationsState.pagination = response.data?.pagination || null;

  } catch (err: unknown) {
    annotationsState.error = getApiError((err as ApiError).code);
    annotationsState.annotations = [];
  } finally {
    annotationsState.loading = false;
  }
}

/**
 * Create a new annotation and if successful update state
 * @param newAnnotation AnnotationCreate
 */
export async function addAnnotation(newAnnotation: AnnotationCreate): Promise<Annotation | null | undefined> {
  annotationsState.loading = true;
  annotationsState.error = null;
  try {
    // Using the new generated SDK directly
    const response = await createAnnotation({
      body: newAnnotation
    });

    if (response.error) {
      throw response.error;
    }

    const createdAnnotation = response.data?.annotation;

    if (createdAnnotation) {
      annotationsState.annotations = [
        ...annotationsState.annotations,
        createdAnnotation
      ];
      annotationsState.currentAnnotationId = createdAnnotation.id;
    }

    return createdAnnotation;
  } catch (error: unknown) {
    annotationsState.error = getApiError((error as ApiError).code);
    return null;
  } finally {
    annotationsState.loading = false;
  }
}

/**
 * Update an annotation and if successful update state
 * @param id The annotation ID to update
 * @param update The annotation update
 */
export async function editAnnotation(id: string, update: AnnotationUpdate): Promise<Annotation | null | undefined> {
  annotationsState.loading = true;
  annotationsState.error = null;
  try {
    // Using the new generated SDK directly
    // The server expects the id in the body of the request as well as the query
    // Use type assertion since AnnotationUpdate type doesn't include id field
    const response = await updateAnnotation({
      body: { ...update, id } as any,
      query: { id }
    });

    if (response.error) {
      throw response.error;
    }

    const updatedAnnotation = response.data?.annotation;
    console.log("annotation updated", { annotation: updatedAnnotation });

    if (updatedAnnotation) {
      const annotationIndex = annotationsState.annotations.findIndex(
        (a) => a.id === updatedAnnotation.id
      );
      if (annotationIndex !== -1) {
        const newAnnotations = [...annotationsState.annotations];
        newAnnotations[annotationIndex] = updatedAnnotation;
        annotationsState.annotations = newAnnotations;
      }
    }
    return updatedAnnotation || null;
  } catch (error: unknown) {
    annotationsState.error = getApiError((error as ApiError).code);
    return null;
  } finally {
    annotationsState.loading = false;
  }
}

/**
 * Delete an annotation and if successful update state
 * @param id The annotation ID to delete
 */
export async function removeAnnotation(id: string) {
  annotationsState.loading = true;
  annotationsState.error = null;
  try {
    // Using the new generated SDK directly
    const response = await deleteAnnotation({
      query: { id }
    });

    if (response.error) {
      throw response.error;
    }

    console.log("annotation deleted", { id });

    // Filter out the deleted annotation from state
    annotationsState.annotations = annotationsState.annotations.filter(
      (a) => a.id !== id
    );
  } catch (error: unknown) {
    annotationsState.error = getApiError((error as ApiError).code);
  } finally {
    annotationsState.loading = false;
  }
}

export function setDraftAnnotation(annotation: AnnotationCreate | null) {
  annotationsState.draft = annotation;
  // make sure other views are not selected // TODO needs cleaner solution
  annotationsState.editingAnnotationId = null;
}

export function setCurrentAnnotationId(id: string | null) {
  annotationsState.currentAnnotationId = id;
  annotationsState.draft = null;
  annotationsState.editingAnnotationId = null;
}

export function setHoveredAnnotationId(id: string | null) {
  annotationsState.hoveredAnnotationId = id;
}

export function setEditingAnnotationId(id: string | null) {
  annotationsState.editingAnnotationId = id;
  annotationsState.draft = null;
}

export function setHighlightedAnnotationId(id: string | null) {
  annotationsState.highlightedAnnotationId = id;
}

// A utility function to get annotations that are currently in the viewport
export function getAnnotationsInView() {
  // This would be implemented based on viewport tracking logic
  // For now, return all annotations
  return annotationsState.annotations;
}
