<script lang="ts">
  import { onMount, onDestroy } from "svelte";
  import {
    addAnnotation,
    setDraftAnnotation,
    authState,
    selectionState,
    setCurrentSelection,
    documentState,
    subscriptionState,
    setSidebarOpen,
    annotationsState,
    setCurrentAnnotationId,
    addDocument,
    instanceState,
    settingsState,
  } from "./states";
    import { push } from "svelte-spa-router";
    import { getDocumentTitle } from "./utils/meta";

  let anchorElement = $state<HTMLElement | null>(null);
  let contextMenu = $state<HTMLElement | null>(null);
  let visible = $state(false);
  let position = $state<"above" | "below">("above");

  let lastTouchOrMouseUpTime = 0;

  function createAnchorElement(range: Range): HTMLElement | null {
    cleanupAnchorElement();

    const anchor = document.createElement("div");
    const bounds = range.getBoundingClientRect();

    anchor.style.position = "fixed";
    anchor.style.width = "1px";
    anchor.style.height = "1px";
    anchor.style.pointerEvents = "none";
    anchor.style.opacity = "0";

    const centerX = bounds.left + bounds.width / 2;
    anchor.style.left = `${centerX}px`;
    anchor.style.top = `${bounds.top}px`;

    document.body.appendChild(anchor);
    return anchor;
  }

  function cleanupAnchorElement() {
    if (anchorElement && anchorElement.parentNode) {
      anchorElement.parentNode.removeChild(anchorElement);
      anchorElement = null;
    }
  }

  function remToPx(rem: number): number {
    return (
      rem * parseFloat(getComputedStyle(document.documentElement).fontSize)
    );
  }

  function updatePosition() {
    if (!contextMenu || !anchorElement) return;

    const menuRect = contextMenu.getBoundingClientRect();
    const selectionRect =
      selectionState.currentSelection?.range?.getBoundingClientRect() ||
      new DOMRect();
    const anchorRect = anchorElement.getBoundingClientRect();

    const arrowHeight = remToPx(0.5);
    const spacing = remToPx(0);
    const edgeMargin = remToPx(1);

    let left = anchorRect.left - menuRect.width / 2;

    let top;

    const roomAbove =
      selectionRect.top - (spacing + menuRect.height + arrowHeight);
    const roomBelow =
      window.innerHeight -
      (selectionRect.bottom + spacing + menuRect.height + arrowHeight);

    if (roomAbove >= 0 || roomBelow < 0) {
      position = "above";
      top = selectionRect.top - spacing - menuRect.height - arrowHeight;
    } else {
      position = "below";
      top = selectionRect.bottom + spacing + arrowHeight;
    }

    left = Math.max(edgeMargin, left);
    left = Math.min(left, window.innerWidth - menuRect.width - edgeMargin);

    if (top < edgeMargin) {
      top = edgeMargin;
    } else if (top + menuRect.height > window.innerHeight - edgeMargin) {
      top = window.innerHeight - menuRect.height - edgeMargin;
    }

    contextMenu.style.left = `${left}px`;
    contextMenu.style.top = `${top}px`;
  }

  let lastProcessedSelection = $state<string | null>(null);
  let lastSelectionState = $state({ hasSelection: false, isSelecting: false });

  $effect(() => {
    const selection = selectionState.currentSelection;
    const isSelecting = selectionState.isSelecting;

    const currentState = {
      hasSelection: !!selection?.range,
      isSelecting,
    };

    if (
      lastSelectionState.hasSelection === currentState.hasSelection &&
      lastSelectionState.isSelecting === currentState.isSelecting
    ) {
      return;
    }

    lastSelectionState = currentState;

    if (selection?.range && !isSelecting) {
      if (selection.quote !== lastProcessedSelection) {
        lastProcessedSelection = selection.quote;
        cleanupAnchorElement();
        anchorElement = createAnchorElement(selection.range);
      }

      visible = true;

      setTimeout(() => {
        if (visible) updatePosition();
      }, 0);
    } else {
      visible = false;
      cleanupAnchorElement();
      lastProcessedSelection = null;
    }
  });

  function handleOutsideClick(event: MouseEvent) {
    const now = Date.now();
    if (now - lastTouchOrMouseUpTime < 300) {
      return;
    }

    if (
      visible &&
      contextMenu &&
      !contextMenu.contains(event.target as Node) &&
      anchorElement &&
      !anchorElement.contains(event.target as Node)
    ) {
      visible = false;
    }
  }

  function handleMouseUpOrTouchEnd() {
    lastTouchOrMouseUpTime = Date.now();
  }

  let scrollRAFPending = false;

  function handleScroll() {
    if (scrollRAFPending) return;
    scrollRAFPending = true;
    requestAnimationFrame(() => {
      scrollRAFPending = false;
      if (visible && selectionState.currentSelection?.range) {
        anchorElement = createAnchorElement(
          selectionState.currentSelection.range,
        );
        updatePosition();
      }
    });
  }

  async function handleHighlight(event: Event) {
    event.stopPropagation();
    event.preventDefault();

    if(!documentState.document) {
      console.log("Document is not available. We try to create the document now.");
      const createdDocument = await addDocument({
        url: settingsState.pageUrl ?? "",
        instance_id: instanceState.instance?.id ?? "",
        title: getDocumentTitle(),
      });
      if (!createdDocument) throw new Error("Failed to create document");
      documentState.document = createdDocument;
    }
    if(!authState.user?.id) throw new Error("User is not authenticated");
    if(!selectionState.currentSelection?.quote) throw new Error("Selection is not available");
    if(!selectionState.currentSelection?.selectors) throw new Error("Selectors are not available");
    const newAnnotation = {
      quote: selectionState.currentSelection.quote,
      document_id: documentState.document?.id,
      selectors: selectionState.currentSelection.selectors,
      profile_id: authState.user.id,
    };
    try {
      const createdAnnotation = await addAnnotation(newAnnotation);
      if (!createdAnnotation) throw new Error("Failed to create annotation");
      setCurrentSelection(null);
      setCurrentAnnotationId(createdAnnotation.id);
      push(`/annotation/${createdAnnotation.id}`);
    } catch (error) {
      console.error("Error creating annotation:", error);
    }
  }

  function handleAnnotate(event: Event) {
    event.stopPropagation();
    event.preventDefault();

    const documentId = documentState.document?.id;
    
    setDraftAnnotation({
      quote: selectionState.currentSelection?.quote ?? "",
      document_id: documentId || "",
      selectors: selectionState.currentSelection?.selectors ?? [],
      profile_id: authState.user?.id ?? "",
      attribute_to: settingsState.pageUrl,
    });
    setSidebarOpen(true);
    push("/draft");
  }

  function handleShare(event: Event) {
    event.stopPropagation();
    event.preventDefault();
    console.log("[not implemented] Share action triggered");
    alert("Share action triggered")
  }

  function handleLogin(event: Event) {
    event.stopPropagation();
    event.preventDefault();
    setSidebarOpen(true);
    push("/login");
  }

  function handleSubscription(event: Event) {
    event.stopPropagation();
    event.preventDefault();
    setSidebarOpen(true);
    push("/subscription");
  }

  function handleKeyDown(event: KeyboardEvent) {
    if (event.key === "Enter" || event.key === " ") {
      handleHighlight(event);
    }
  }

  onMount(() => {
    window.addEventListener("click", handleOutsideClick);
    window.addEventListener("scroll", handleScroll);
    document.addEventListener("mouseup", handleMouseUpOrTouchEnd);
    document.addEventListener("touchend", handleMouseUpOrTouchEnd);
  });

  onDestroy(() => {
    window.removeEventListener("click", handleOutsideClick);
    window.removeEventListener("scroll", handleScroll);
    document.removeEventListener("mouseup", handleMouseUpOrTouchEnd);
    document.removeEventListener("touchend", handleMouseUpOrTouchEnd);
    cleanupAnchorElement();
  });
</script>

{#if visible}
  <div
    bind:this={contextMenu}
    class="context-menu"
    role="button"
    tabindex="0"
    onkeydown={handleKeyDown}
    aria-label="Annotate selection"
  >
    <div class="menu-content">
      {#if authState.isAuthenticated && subscriptionState.subscription?.moderation === "approved"}
        <button
          onclick={handleHighlight}
          disabled={annotationsState.loading}
          aria-label="Highlight selection"
        >
          Highlight
        </button>
        <button
          onclick={handleAnnotate}
          disabled={annotationsState.loading}
          aria-label="Annotate selection"
        >
          Annotate
        </button>
        <button
          onclick={handleShare}
          aria-label="Share selection"
        >
          Share
        </button>
      {:else if authState.isAuthenticated && subscriptionState.subscription?.moderation === "pending"}
        <button onclick={handleSubscription} aria-label="Annotate selection">
          Pending subscription to highlight & annotate
        </button>
      {:else if authState.isAuthenticated && subscriptionState.subscription?.moderation === "rejected"}
        <button onclick={handleSubscription} aria-label="Annotate selection">
          Your subscription request was rejected
        </button>
      {:else if authState.isAuthenticated && !subscriptionState.subscription}
        <button onclick={handleSubscription} aria-label="Annotate selection">
          Subscribe to highlight & annotate
        </button>
      {:else}
        <button onclick={handleLogin} aria-label="Annotate selection">
          Login to highlight & annotate
        </button>
      {/if}
    </div>
    <div class={`menu-arrow ${position}`}></div>
  </div>
{/if}

<style>
  .context-menu {
    position: fixed;
    font-size: 0.825rem;
    background-color: var(--color-grey-400);
    border-radius: 0.25rem;
    max-width: 90vw;
    padding: 0 1rem;
    z-index: 1000;
    opacity: 1; /* Start visible */
    transform: translateY(-0.5rem); /* No initial offset */
    transition:
      opacity 0.2s ease,
      transform 0.2s ease;
    pointer-events: auto;
  }

  @keyframes fade-in {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .menu-arrow {
    position: absolute;
    width: 1rem;
    height: 1rem;
    background-color: inherit;
    transform: rotate(45deg);
    left: calc(50% - 0.5rem);
  }

  .menu-arrow.above {
    bottom: -0.5rem;
  }

  .menu-arrow.below {
    top: -0.5rem;
    transform: rotate(-135deg);
  }

  .menu-content {
    border-radius: 0.5rem;
    background-color: inherit;
    position: relative;
    z-index: 2;
    display: flex;
    gap: 0.25rem;
    align-items: center;
    justify-content: center;
  }

  button {
    padding: 0.5rem;
    background: transparent;
    border: none;
    cursor: pointer;
    border-radius: 0.25rem;
    margin: 0;
    font-size: 0.825rem;
    font-family: var(--font-sans);
    color: var(--text-color);
  }
  button:hover {
    text-decoration: underline;
  }
</style>
