<script lang="ts">
  import { formatTimeAgo } from "./utils/date";
  import AnnotationItemControls from "./AnnotationItemControls.svelte";
  import AnnotationEdit from "./AnnotationEdit.svelte";
  import {
    setHighlightedAnnotationId,
    annotationsState,
    uiState,
    settingsState,
    setEditingAnnotationId,
  } from "./states";
  import Comment from "./icons/Comment.svelte";
  import type { Annotation } from "./api";
  import ReplyForm from "./ReplyForm.svelte";
  import RepliesList from "./RepliesList.svelte";
  import TextButton from "./common/TextButton.svelte";

  import { push } from "svelte-spa-router";
    import { LockLockedIcon } from "$lib/icons";

  const { annotation } = $props<{ annotation: Annotation }>();
  
  let isEditMode = $derived(annotationsState.editingAnnotationId === annotation.id);

  function handleOpenDetails() {
    if (annotationsState.currentAnnotationId === annotation.id) {
    } else {
      push(`/annotation/${annotation.id}`);
    }
  }

  function handleMouseOver() {
    setHighlightedAnnotationId(annotation.id);
  }

  function handleMouseOut() {
    setHighlightedAnnotationId(null);
  }

  function handleEditCancel() {
    setEditingAnnotationId(null);
  }

  function handleEditSaved() {
    setEditingAnnotationId(null);
    // The annotation is already updated in the state by the editAnnotation function
  }

  let timeAgo = $derived(
    annotation.created_at ? formatTimeAgo(new Date(annotation.created_at)) : "",
  );

  let indicator = $derived(
    uiState.indicators.find((indicator) =>
      indicator.annotations.some((a) => a.id === annotation.id),
    ),
  );

  let offsetY = $derived(indicator ? indicator.y : 0);
</script>

<div
  class="item {annotationsState.hoveredAnnotationId === annotation.id
    ? 'hovered'
    : ''} {annotationsState.currentAnnotationId === annotation.id
    ? 'selected'
    : ''} {annotation.privacy === 'private' ? 'private' : 'public'}"
  style={`top: ${offsetY}px; left: 0;`}
  aria-label={`Annotation by ${annotation.profile?.full_name || "Anonymous"}`}
>
  {#if annotation.quote}
    <button
      class="quote"
      onclick={handleOpenDetails}
      tabindex="0"
      aria-label="Highlight annotation"
    >
    {#if annotation.privacy === "private"}
      <div class="privacy">
        <LockLockedIcon size={16} />
      </div>
    {/if}
      <span>{annotation.quote}</span>
    </button>
  {/if}

  <div class="meta">
    <div class="left">
      <div class="info">
        <a href={settingsState.serverUrl + "/p/" + annotation.profile?.username}>
          <span class="name"
            >{annotation.profile?.full_name || "Anonymous"}</span
          >
        </a>
        <span class="time">{timeAgo}</span>
      </div>
    </div>
    <div class="right">
      <AnnotationItemControls {annotation} />
    </div>
  </div>

  {#if isEditMode}
    <AnnotationEdit {annotation} onCancel={handleEditCancel} onSaved={handleEditSaved} />
  {:else}
    <div class="text">
      {annotation.text}
    </div>
  {/if}
  <div class="actions">
    {#if annotation.repliesCount && annotationsState.currentAnnotationId !== annotation.id}
      <TextButton
        size="xs"
        tabindex="0"
        onclick={handleOpenDetails}
        onmouseover={handleMouseOver}
        onmouseout={handleMouseOut}
        onkeydown={(e: KeyboardEvent) =>
          e.key === "Enter" && handleOpenDetails()}
      >
        <Comment color="var(--text-color)" /> &nbsp; Show {annotation.repliesCount ||
          0} repl{annotation.repliesCount === 1 ? "y" : "ies"}
      </TextButton>
    {/if}
  </div>
  <ReplyForm annotationId={annotation.id} />
  <RepliesList annotationId={annotation.id} />
</div>

