    position: absolute;
  }

  .meta {
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    padding: 0;
    justify-content: space-between;
  }

  .quote {
    padding: 1rem;
    border: 0;
    border-radius: 2rem;
    display: block;
    width: 100%;
    box-sizing: border-box;
    text-align: left;
    cursor: pointer;
    font-family: var(--font-serif);
    font-size: 1.25rem;
    line-height: 1.3;
    margin-bottom: 1rem;
    background-color: var(--color-primary-100);
    transition:
      transform 0.2s ease,
      box-shadow 0.2s ease;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  }
  .quote:hover {
    transform: translateY(-1px);
    box-shadow: 1px 1.5px 1px rgba(0, 0, 0, 0.15);
  }
  .quote span {
    background-color: rgb(239, 158, 255);
    border-left: .2rem solid rgb(239, 158, 255);
    border-right: .2rem solid rgb(239, 158, 255);
  }
  .hovered .quote span {
    background-color: rgba(212, 0, 255);
    border-color: rgba(212, 0, 255);
  }
  .privacy {
    display: inline;
  }

  .item.private .quote {
    background: var(--clr-warning-b0);
  }

  .item.public .quote {
    background: var(--clr-neutral-a0);
  }

  .text {
    padding: 0 1rem 1rem 1rem;
