    font-weight: 600;
  }
  .info .time {
    margin: 0;
    font-size: 0.825rem;
    color: var(--color-grey-600);
  }

  .actions {
    font-size: 0.8em;
    display: flex;
    justify-content: flex-end;
  }
</style>
