/**
 * Color allocator utility for highlight clustering
 * Assigns different colors to overlapping highlights using a greedy graph coloring algorithm
 */

import type { Selector } from "../../types/selectors";

// Type definitions
export interface RangeInfo {
  id: string;
  range: Range;
  colorIndex?: number;
}

export interface ColorAllocation {
  [annotationId: string]: number; // Maps annotation ID to color index (0-8)
}

/**
 * Detects if two DOM ranges overlap
 * @param range1 First DOM range
 * @param range2 Second DOM range
 * @returns True if ranges overlap, false otherwise
 */
export function rangesOverlap(range1: Range, range2: Range): boolean {
  // Quick check - if ranges are in completely different parts of the document
  if (
    range1.commonAncestorContainer !== range2.commonAncestorContainer &&
    !range1.commonAncestorContainer.contains(range2.commonAncestorContainer) &&
    !range2.commonAncestorContainer.contains(range1.commonAncestorContainer)
  ) {
    return false;
  }

  // Check for overlap using DOM Range comparison
  // Two ranges overlap if neither is before or after the other
  return !(
    range1.compareBoundaryPoints(Range.END_TO_START, range2) > 0 ||
    range1.compareBoundaryPoints(Range.START_TO_END, range2) < 0
  );
}

/**
 * Determines which color to use for a highlight based on overlapping ranges
 * Uses a greedy graph coloring algorithm to assign the lowest available color
 * @param newRange The range to assign a color to
 * @param existingRanges Existing ranges with their assigned colors
 * @param maxColors Maximum number of colors available (default: 9 for color-0 through color-8)
 * @returns Color index to use (0-8)
 */
export function getColorForRange(
  newRange: Range,
  existingRanges: RangeInfo[],
  maxColors: number = 9
): number {
  // Find all overlapping ranges
  const overlappingRanges = existingRanges.filter((info) =>
    rangesOverlap(newRange, info.range)
  );

  // If no overlaps, use the first color
  if (overlappingRanges.length === 0) {
    return 0;
  }

  // Get colors already used by overlapping ranges
  const usedColors = new Set<number>();
  overlappingRanges.forEach((info) => {
    if (info.colorIndex !== undefined) {
      usedColors.add(info.colorIndex);
    }
  });

  // Find the lowest unused color
  for (let i = 0; i < maxColors; i++) {
    if (!usedColors.has(i)) {
      return i;
    }
  }

  // If all colors are used, cycle back to the first color
  // In a real-world scenario, you might want to implement a more sophisticated
  // fallback strategy, like using opacity variations or patterns
  return 0;
}

/**
 * Maintains color allocations for annotations across refreshes
 */
export class HighlightColorManager {
  private colorAllocations: ColorAllocation = {};
  private rangeRegistry: Map<string, RangeInfo> = new Map();
  private maxColors: number;

  constructor(maxColors: number = 9) {
    this.maxColors = maxColors;
  }

  /**
   * Registers a range and gets its color index
   * @param annotationId The annotation ID
   * @param range The DOM range
   * @returns Color index to use
   */
  public getColorForAnnotation(annotationId: string, range: Range): number {
    // If we already have a color allocated for this annotation, reuse it
    if (this.colorAllocations[annotationId] !== undefined) {
      return this.colorAllocations[annotationId];
    }

    // Get existing ranges
    const existingRanges = Array.from(this.rangeRegistry.values());

    // Determine color
    const colorIndex = getColorForRange(range, existingRanges, this.maxColors);

    // Store allocation
    this.colorAllocations[annotationId] = colorIndex;
    this.rangeRegistry.set(annotationId, {
      id: annotationId,
      range,
      colorIndex,
    });

    return colorIndex;
  }

  /**
   * Clears all registered ranges but keeps color allocations
   * Useful when refreshing highlights
   */
  public clearRanges(): void {
    this.rangeRegistry.clear();
  }

  /**
   * Gets all current color allocations
   */
  public getAllocations(): ColorAllocation {
    return { ...this.colorAllocations };
  }

  /**
   * Resets all color allocations
   */
  public reset(): void {
    this.colorAllocations = {};
    this.rangeRegistry.clear();
  }
}

// Export a singleton instance for app-wide use
export const highlightColorManager = new HighlightColorManager();
