/**
 * Utility for working with text ranges in the DOM.
 * Adapted from Hypothesis's text-range implementation.
 */

/**
 * Return the combined length of text nodes contained in `node`.
 */
function nodeTextLength(node: Node): number {
  switch (node.nodeType) {
    case Node.ELEMENT_NODE:
    case Node.TEXT_NODE:
      // `textContent` excludes text in comments and processing instructions
      return node.textContent?.length ?? 0;
    default:
      return 0;
  }
}

/**
 * Return the total length of the text of all previous siblings of `node`.
 */
function previousSiblingsTextLength(node: Node): number {
  let sibling = node.previousSibling;
  let length = 0;
  while (sibling) {
    length += nodeTextLength(sibling);
    sibling = sibling.previousSibling;
  }
  return length;
}

/**
 * Resolve character offsets within an element to (text node, position) pairs.
 *
 * @param element - The container element
 * @param offsets - Offsets in ascending order
 */
function resolveOffsets(
  element: Element,
  ...offsets: number[]
): Array<{ node: Text; offset: number }> {
  let nextOffset = offsets.shift();
  const nodeIter = element.ownerDocument.createNodeIterator(
    element,
    NodeFilter.SHOW_TEXT
  );
  const results = [];

  let currentNode = nodeIter.nextNode() as Text | null;
  let textNode;
  let length = 0;

  // Find the text node containing each offset
  while (nextOffset !== undefined && currentNode) {
    textNode = currentNode;
    if (length + textNode.data.length > nextOffset) {
      results.push({ node: textNode, offset: nextOffset - length });
      nextOffset = offsets.shift();
    } else {
      currentNode = nodeIter.nextNode() as Text | null;
      length += textNode.data.length;
    }
  }

  // Boundary case for offset at the end of text
  while (nextOffset !== undefined && textNode && length === nextOffset) {
    results.push({ node: textNode, offset: textNode.data.length });
    nextOffset = offsets.shift();
  }

  if (nextOffset !== undefined) {
    throw new RangeError('Offset exceeds text length');
  }

  return results;
}

/**
 * Direction to search for the nearest text node.
 */
export enum ResolveDirection {
  FORWARDS = 1,
  BACKWARDS = 2,
}

/**
 * Represents an offset within the text content of an element.
 */
export class TextPosition {
  public element: Element;
  public offset: number;

  constructor(element: Element, offset: number) {
    if (offset < 0) {
      throw new Error('Offset is invalid');
    }

    this.element = element;
    this.offset = offset;
  }

  /**
   * Return a copy of this position relative to a given ancestor element.
   */
  relativeTo(parent: Element): TextPosition {
    if (!parent.contains(this.element)) {
      throw new Error('Parent is not an ancestor of current element');
    }

    let el = this.element;
    let offset = this.offset;
    while (el !== parent) {
      offset += previousSiblingsTextLength(el);
      el = el.parentElement!;
    }

    return new TextPosition(el, offset);
  }

  /**
   * Resolve the position to a specific text node and offset within that node.
   */
  resolve(options: { direction?: ResolveDirection } = {}): {
    node: Text;
    offset: number;
  } {
    try {
      return resolveOffsets(this.element, this.offset)[0];
    } catch (err) {
      if (this.offset === 0 && options.direction !== undefined) {
        const tw = document.createTreeWalker(
          this.element.getRootNode(),
          NodeFilter.SHOW_TEXT
        );
        tw.currentNode = this.element;
        const forwards = options.direction === ResolveDirection.FORWARDS;
        const text = forwards
          ? (tw.nextNode() as Text | null)
          : (tw.previousNode() as Text | null);
        if (!text) {
          throw err;
        }
        return { node: text, offset: forwards ? 0 : text.data.length };
      } else {
        throw err;
      }
    }
  }

  /**
   * Create a TextPosition from a character offset within a node.
   */
  static fromCharOffset(node: Node, offset: number): TextPosition {
    switch (node.nodeType) {
      case Node.TEXT_NODE:
        return TextPosition.fromPoint(node, offset);
      case Node.ELEMENT_NODE:
        return new TextPosition(node as Element, offset);
      default:
        throw new Error('Node is not an element or text node');
    }
  }

  /**
   * Create a TextPosition from a (node, offset) pair.
   */
  static fromPoint(node: Node, offset: number): TextPosition {
    switch (node.nodeType) {
      case Node.TEXT_NODE: {
        if (offset < 0 || offset > (node as Text).data.length) {
          throw new Error('Offset outside text node boundaries');
        }
        const normalized = node.parentElement!;
        return new TextPosition(
          normalized,
          offset + previousSiblingsTextLength(node)
        );
      }
      case Node.ELEMENT_NODE: {
        if (offset < 0 || offset > node.childNodes.length) {
          throw new Error('Offset outside element boundaries');
        }
        let textOffset = 0;
        for (let i = 0; i < offset; i++) {
          textOffset += nodeTextLength(node.childNodes[i]);
        }
        return new TextPosition(node as Element, textOffset);
      }
      default:
        throw new Error('Point is not in an element or text node');
    }
  }
}

/**
 * Represents a range of text in a document.
 */
export class TextRange {
  public start: TextPosition;
  public end: TextPosition;

  constructor(start: TextPosition, end: TextPosition) {
    this.start = start;
    this.end = end;
  }

  /**
   * Return a new TextRange with start and end positions relative to `element`.
   */
  relativeTo(element: Element): TextRange {
    return new TextRange(
      this.start.relativeTo(element),
      this.end.relativeTo(element)
    );
  }

  /**
   * Convert this TextRange to a DOM Range object.
   */
  toRange(): Range {
    const range = new Range();
    const start = this.start.resolve({ direction: ResolveDirection.FORWARDS });
    const end = this.end.resolve({ direction: ResolveDirection.BACKWARDS });
    
    range.setStart(start.node, start.offset);
    range.setEnd(end.node, end.offset);
    
    return range;
  }

  /**
   * Create a TextRange from a DOM Range.
   */
  static fromRange(range: Range): TextRange {
    const start = TextPosition.fromPoint(range.startContainer, range.startOffset);
    const end = TextPosition.fromPoint(range.endContainer, range.endOffset);
    return new TextRange(start, end);
  }

  /**
   * Create a TextRange from character offsets within a root element.
   */
  static fromOffsets(root: Element, start: number, end: number): TextRange {
    return new TextRange(
      new TextPosition(root, start),
      new TextPosition(root, end)
    );
  }

  /**
   * Return a DOM Range with whitespace trimmed from start and end.
   */
  static trimmedRange(range: Range): Range {
    const trimmed = range.cloneRange();
    
    // Find text nodes at the start and end of the range
    const startNode = trimmed.startContainer;
    const endNode = trimmed.endContainer;
    
    // Only trim if we're in text nodes
    if (startNode.nodeType === Node.TEXT_NODE) {
      const startText = (startNode as Text).data;
      let startOffset = trimmed.startOffset;
      
      // Advance start past whitespace
      while (startOffset < startText.length && /\s/.test(startText[startOffset])) {
        startOffset++;
      }
      
      trimmed.setStart(startNode, startOffset);
    }
    
    if (endNode.nodeType === Node.TEXT_NODE) {
      const endText = (endNode as Text).data;
      let endOffset = trimmed.endOffset;
      
      // Move end back past whitespace
      while (endOffset > 0 && /\s/.test(endText[endOffset - 1])) {
        endOffset--;
      }
      
      trimmed.setEnd(endNode, endOffset);
    }
    
    return trimmed;
  }
}
