// wp.svelte.ts
import type { WPAkinUser } from "./types";
import { WPService } from "./service";

const WP_STORAGE_KEY = 'akin_wp_state';

// Create service instance (URL will be set when initialized)
let wpService: WPService;

export let wpState = $state({
  wpUser: null as WPAkinUser | null,
  wpIsLoggedIn: false,
  wpUserId: null as number | null,
  wpError: null as any | null,
  wpIsLoading: false,
  wpLastCheck: null as Date | null,
});

// Initialize the WordPress service with the API URL
export function loadWordpress(apiUrl: string) {
  wpService = new WPService(apiUrl);
  
  console.log(window.akinConfig)
  // Try to load the saved state
  // TODO enable but check if it works first.
  // loadFromLocalStorage();
  
  // Check auth status on initialization
  getAuthStatus();
}

// Save state to localStorage
function persist() {
  if (typeof window === 'undefined') return;
  
  const stateToSave = {
    wpUser: wpState.wpUser,
    wpIsLoggedIn: wpState.wpIsLoggedIn,
    wpUserId: wpState.wpUserId,
    wpError: wpState.wpError,
    wpIsLoading: wpState.wpIsLoading,
    wpLastCheck: wpState.wpLastCheck
  };
  
  localStorage.setItem(WP_STORAGE_KEY, JSON.stringify(stateToSave));
}

// Clear localStorage
function clearLocalStorage() {
  if (typeof window === 'undefined') return;
  localStorage.removeItem(WP_STORAGE_KEY);
}

// Check if the user is authenticated with WordPress
export async function getAuthStatus() {
  if (!wpService) {
    console.error('WPService not initialized. Call initWPService first.');
    return false;
  }
  
  wpState.wpIsLoading = true;
  wpState.wpError = null;
  
  try {
    const { auth, error } = await wpService.getAuthStatus();
    
    wpState.wpIsLoggedIn = auth?.isLoggedIn || false;
    wpState.wpUserId = auth?.userId || null;
    wpState.wpLastCheck = new Date();
    wpState.wpUser = auth?.user || null;
    
    if (error) {
      throw error;
    }
    
    persist();
    wpState.wpIsLoading = false;
    return wpState.wpIsLoggedIn;
  } catch (err) {
    wpState.wpError = err instanceof Error ? err : new Error('Auth check failed');
    wpState.wpIsLoading = false;
    return false;
  }
}

// Helper function to check if user is logged in
export function wpIsLoggedIn() {
  return wpState.wpIsLoggedIn && wpState.wpUser !== null;
}

// Clear the WordPress state (for logout)
export function clearWPState() {
  wpState.wpUser = null;
  wpState.wpIsLoggedIn = false;
  wpState.wpUserId = null;
  wpState.wpError = null;
  wpState.wpIsLoading = false;
  wpState.wpLastCheck = null;
  
  clearLocalStorage();
}