import { sanitizeUrl } from "../utils/url";
import { config } from "../config";

export const settingsState = $state({
  pageUrl: "",
  publicKey: "",
  serverUrl: config.serverUrl,
  selector: "",
  debug: false,
});

export function setPageUrl(pageUrl: string) {
  settingsState.pageUrl = sanitizeUrl(pageUrl);
}

export function setPublicKey(publicKey: string) {
  settingsState.publicKey = publicKey;
}

export function setServerUrl(serverUrl: string) {
  settingsState.serverUrl = serverUrl;
}

export function setSelector(selector: string) {
  settingsState.selector = selector;
}

export function setDebug(debug: boolean) {
  settingsState.debug = debug;
}
  