<svelte:options
  customElement={{
    tag: "ak-in",
    shadow: "none",
  }}
/>

<script lang="ts">
  import "./akin.css";
  import { onMount } from "svelte";
  import Highlighter from "./Highlighter.svelte";
  import Sidebar from "./Sidebar.svelte";
  import TextSelector from "./TextSelector.svelte";
  import Router, { push } from "svelte-spa-router";
  import {
    settingsState,
    instanceState,
    documentState,
    annotationsState,
    loadInstanceByPublicKey,
    loadDocumentByUrl,
    loadAnnotations,
    loadSubscription,
    setPageUrl,
    setPublicKey,
    setServerUrl,
    setIsMobile,
    confirm,
    setDebug,
    addToast,
    dismissToast,
    authState,
    setSelector,
  } from "./states";
  import { routes } from "./routes";
  import Fab from "./Fab.svelte";
  import WP from "./plugins/wp/WP.svelte";
  import Debug from "./Debug.svelte";
  import { api } from "./api";
  import Toasts from "./common/Toasts.svelte";
  import { getApiError } from "./utils/errors";

  // These attributes are stored in the settings state
  const { selector, pageurl, publickey, serverurl, debug } = $props();

  /**
   * Separated reactive effects to prevent circular dependencies
   * Each effect has a focused responsibility and specific triggers
   **/

  // Effect 1: Handle component props (serverurl, selector, debug)
  $effect(() => {
    if (serverurl) {
      setServerUrl(serverurl);
      api.setServerUrl(serverurl);
    }
    if (selector) {
      console.log("Loading instance by selector:", selector);
      setSelector(selector);
      rootElement = selector ? document.querySelector(selector) : document.body;
    }
    if (debug) {
      setDebug(debug);
    }
  });

  // Effect 2: Handle public key and instance loading
  $effect(() => {
    if (publickey) setPublicKey(publickey);
    
    if (settingsState.publicKey) {
      console.log("Loading instance by public key:", settingsState.publicKey);
      loadInstanceByPublicKey(settingsState.publicKey).then((success) => {
        console.log("Instance loaded:", success);
        if (!success) {
          addToast({
            message: "Akin server is down",
            type: "error",
            dismissible: true,
            timeout: 10000,
          });
        }
      });
    }
  });

  // Effect 3: Handle URL and document loading (only when instance is available)
  $effect(() => {
    if (!instanceState.instance?.id) return;
    
    const urlToLoad = pageurl || (typeof window !== "undefined" ? window.location.origin + window.location.pathname : null);
    if (urlToLoad) {
      setPageUrl(urlToLoad);
      loadDocumentByUrl(instanceState.instance.id, urlToLoad);
    }
  });

  // Effect 4: Handle subscription loading when instance changes
  $effect(() => {
    if (instanceState.instance) {
      loadSubscription();
    }
  });

  // Effect 5: Handle annotation loading when document changes
  $effect(() => {
    if (documentState.document?.id) {
      loadAnnotations({ documentId: documentState.document.id });
    } else {
      annotationsState.annotations = [];
    }
  });

  // Effect 6: Handle document errors
  $effect(() => {
    if (documentState.error) {
      console.warn("Failed to load document:", documentState.error);
    }
  });

  // Effect 7: Handle window resize events
  $effect(() => {
    if (typeof window === "undefined") return;
    
    function handleResize() {
      setIsMobile(window.innerWidth < 768);
    }
    
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  });

  let rootElement = $state<Element | null>(null);

  onMount(async () => {
    console.log("Mounting Akin component with: ", {
      publickey,
      pageurl,
      serverurl,
      selector,
    });

    // Get token_hash from url on initial load (receiving confirm login request)
    // Check both query parameters and hash fragment for token_hash
    let tokenHash = null;

    // First check query parameters
    const urlParams = new URLSearchParams(window.location.search);
    tokenHash = urlParams.get("token_hash");

    // If not found in query parameters, check hash fragment
    if (!tokenHash && window.location.hash) {
      // Remove the leading # and split by & to get key-value pairs
      const hashParams = window.location.hash.substring(1).split("&");

      // Find the token_hash parameter in the hash
      for (const param of hashParams) {
        const [key, value] = param.split("=");
        if (key === "token_hash") {
          tokenHash = value;
          break;
        }
      }
    }

    if (tokenHash) {
      const toastId = addToast({
        message: "Confirming login...",
        type: "info",
        dismissible: false,
        timeout: 0,
      });
      const success = await confirm(tokenHash, settingsState.publicKey);
      dismissToast(toastId);
      if (success) {
        addToast({
          message: "Login confirmed!",
          type: "success",
          dismissible: true,
          timeout: 3000,
        });
      } else {
        addToast({
          message:
            getApiError(authState.error?.code, {
              scope: "auth",
              table: "subscriber",
            }).message || "Login failed",
          type: "error",
          dismissible: true,
          timeout: 10000,
        });
      }
    }
    // redirect to home or depending on token issue again to login or recovery
    push("/");
  });
</script>

{#if instanceState.instance}
  <div class="akin">
    <TextSelector {rootElement} />
    <Highlighter {rootElement} />
    <Fab />
    <Sidebar>
      <Router {routes} />
    </Sidebar>
  </div>
  
  <style>
    .akin {
      /** Assuming the parent is flex */
      width: 100%;
      align-self: stretch;
      display: flex;
    }
    </style>
{/if}

<Toasts />
<Debug />
<WP />
