import { type Document, type DocumentCreate, getDocuments, createDocument, type ApiError } from "../api";
import { getApiError } from "../utils/errors";

export let documentState = $state({
  document: null as Document | null,
  error: null as ApiError | null,
  loading: false,
});

interface DocumentError {
  error: ApiError;
}

/**
 * Load a document by URL
 * @param instanceId - The ID of the instance (required to not confuse documents from different instances)
 * @param url - The URL of the document
 */
export async function loadDocumentByUrl(instanceId: string, url: string) {
  documentState.loading = true;
  try {
    const {data: foundDocument, error} = await getDocuments({ query: {
      instance_id: instanceId,
      url: encodeURIComponent(url)
    } });
    if(error) {
      throw (error as unknown as DocumentError).error;
    }
    if(!foundDocument || !Array.isArray(foundDocument) || foundDocument.length === 0) {
      throw new TypeError("No document found");
    } else {
      documentState.document = foundDocument[0];
    }
  } catch (error: unknown) {
    documentState.error = getApiError((error as ApiError).code, { scope: 'client', table: 'document' });
  } finally {
    documentState.loading = false;
  }
}

/**
 * Add a document
 * @param document - The document to add
 */
export async function addDocument(document: DocumentCreate): Promise<Document | null> {
  documentState.loading = true;
  try {
    const { data, error } = await createDocument({ body: document });
    if(error) {
      throw error;
    }
    if(!data) {
      throw new Error("No document created");
    }
    documentState.document = data;
    return data;
  } catch (error: unknown) {
    documentState.error = getApiError((error as ApiError).code, { scope: 'client', table: 'document' });
    return null;
  } finally {
    documentState.loading = false;
  }
}