/**
 * Formats a date to a human-readable "time ago" string client side
 * alternatively use date-fns
 * @param date The date to format
 * @returns Formatted string like "2h ago", "5d ago", etc.
 */
export function formatTimeAgo(date: Date): string {
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffSec = Math.floor(diffMs / 1000);
  const diffMin = Math.floor(diffSec / 60);
  const diffHour = Math.floor(diffMin / 60);
  const diffDay = Math.floor(diffHour / 24);
  const diffMonth = Math.floor(diffDay / 30);
  const diffYear = Math.floor(diffMonth / 12);

  if (diffYear > 0) return `${diffYear} years ago`;
  if (diffMonth > 0) return `${diffMonth} months ago`;
  if (diffDay > 0) return `${diffDay} days ago`;
  if (diffHour > 0) return `${diffHour} hours ago`;
  if (diffMin > 0) return `${diffMin} minutes ago`;
  return "just now";
}

export function formatDate(date: Date | string): string {
  if (typeof date === "string") {
    date = new Date(date);
  }
  const options: Intl.DateTimeFormatOptions = {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
    // second: "2-digit",
  };
  return date.toLocaleString("en-US", options);
}
