<script lang="ts">
    import { onMount } from "svelte";
    import Barbie from "./Barbie.svelte";
    import Goldcard from "./Goldcard.svelte";
    import Ryanair from "./Ryanair.svelte";
    import Ups from "./Ups.svelte";

    export let id: string = "";

    type AdId = "barbie" | "goldcard" | "ryanair" | "ups";

    const adIds: AdId[] = ["barbie", "goldcard", "ryanair", "ups"];

    const components: { [key in AdId]: any } = {
        barbie: Barbie,
        goldcard: Goldcard,
        ryanair: Ryanair,
        ups: Ups,
    };

    let currentId: AdId = "ryanair";

    onMount(() => {
        if (!id) {
            // Randomly select an ad if no ID was provided
            const randomIndex = Math.floor(Math.random() * adIds.length);
            currentId = adIds[randomIndex];
        }
    });

    $: currentId = id ? (id.toLowerCase() as AdId) : currentId;
</script>

<div class="ad">
    <svelte:component this={components[currentId]} />
</div>

<style>
    .ad {
        box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.1);
        border-radius: 1rem;
        overflow: hidden;
    }
</style>
