<script lang="ts">
  import { repliesState, annotationsState, loadReplies } from "./states";
  import ReplyItem from "./ReplyItem.svelte";

  // The replies are now directly passed from the parent component
  const { annotationId } = $props();

  $effect(() => {
    if (annotationId === annotationsState.currentAnnotationId) {
      loadReplies();
    }
  });
</script>

{#if annotationId === annotationsState.currentAnnotationId}
  <div class="replies">
    {#if repliesState.replies.length > 0}
      {#each repliesState.replies as reply (reply.id)}
        <ReplyItem {reply} />
      {/each}
    {:else if repliesState.loading }
      <div class="no-replies">
        <p>Loading replies...</p>
      </div>
    {:else}
      <div class="no-replies">
        <p>No replies yet</p>
      </div>
    {/if}
  </div>
{/if}

<style>
  .replies {
    margin-top: 0.5rem;
    padding-left: 2rem;
    position: relative;
  }
  .replies::before {
    content: "";
    position: absolute;
    left: .9rem;
    top: 1rem;
    bottom: 2rem;
    width: .2rem;
    background-color: var(--color-grey-300);
    border-radius: .25rem;
  }

  .no-replies {
    font-size: 0.8rem;
    color: var(--color-grey-500);
    font-style: italic;
    padding: 0.25rem 0;
  }
</style>
