<script lang="ts">
    import Header from "../header/Header.svelte";
    import AnnotationDetail from "../AnnotationDetail.svelte";
    import AnnotationForm from "../AnnotationForm.svelte";
    import { setCurrentAnnotationId } from '../states';
    import { onMount } from 'svelte';

    // Define interface for route parameters
    interface RouteParams {
        id?: string;
    }
    
    // Use $props() for component props in runes mode
    const { params = {} as RouteParams } = $props();
    
    // Use $effect to react to changes in params
    $effect(() => {
        if (params && params.id) {
            console.log('Setting current annotation ID from URL parameter:', params.id);
            setCurrentAnnotationId(params.id);
        } else {
            console.log('No annotation ID in URL, clearing current annotation');
            setCurrentAnnotationId(null);
        }
    });
</script>

<Header />
<AnnotationDetail />
<AnnotationForm />