// instances.svelte.ts
import { type Instance, getInstance} from '../api'

export let instanceState = $state({
  instance: null as Instance | null,
  instanceError: null as Error | null,
});

export async function loadInstanceByPublicKey(key: string): Promise<boolean> {
  try {
    const _instance = await getInstance({
      query: {
        public_key: key
      }
    });
    if (_instance.data) {
      console.log("Instance loaded:", _instance.data);
      instanceState.instance = _instance.data; // Mutate property, don't reassign
      instanceState.instanceError = null;
    } else {
      instanceState.instance = null;
      instanceState.instanceError = new Error("Instance not found");
    }
  } catch (error) {
    instanceState.instance = null;
    instanceState.instanceError =
      error instanceof Error ? error : new Error("Unknown error");
  }

  return !!instanceState.instance;
}

export function clearInstance() {
  instanceState.instance = null;
  instanceState.instanceError = null;
}
