export const getDocumentTitle = (): string => {
  // Try h1 first
  const h1 = document.querySelector("h1");
  if (h1) {
    return h1.textContent!;
  }
  // Try title of document
  const title = document.querySelector("title");
  if (title) {
    return title.textContent!;
  }
  return "Untitled";
};