import { PersistentState } from '@friendofsvelte/state';
import { api, signup, confirmSignup, logout,type Profile } from "../api";
import { addToast, settingsState } from "./../states";
import type { ApiError } from "../api";
import { getApiError } from '../utils/errors';

// Set API base URL using the environment variable
// TODO: this should be done more global since in each state it could be redundant.
api.setServerUrl(`${settingsState.serverUrl}/api/v1`);

const AUTH_STORAGE_KEY = 'akin_auth_state';
const SESSION_TIMEOUT = 24 * 60 * 60 * 1000; // 24 hours by default

const _authState = new PersistentState(AUTH_STORAGE_KEY, {
  user: null as Profile | null,
  isAuthenticated: false,
  error: undefined as ApiError | unknown,
  isLoading: false,
  lastLogin: null as Date | null,
});
export const authState = _authState.current;

export async function login(email: string, publicKey: string, wpId?: number, username?: string, redirect_url?: string): Promise<boolean> {
  authState.isLoading = true;
  authState.error = undefined;
  
  try {
    console.log("Login ", username, email, publicKey, wpId, redirect_url);
    const { data, error } = await signup({
      body: {
        email,
        wp_id: wpId,
        username,
        public_key: publicKey,
        redirect_url,
      },
    });
    authState.isLoading = false;
    if (error) {
      throw error.error;
    }
    return true;
  } catch (err: ApiError | unknown) {
    authState.error = err;
    console.log(authState.error);
    authState.isLoading = false;
    return false;
  }
}

export async function confirm(tokenHash: string, publicKey: string) {
  authState.isLoading = true;
  authState.error = null;
  
  try {
    console.log("Confirm ", tokenHash, publicKey);
    const { data, error } = await confirmSignup({
      body: {
        token_hash: tokenHash,
        public_key: publicKey,
      },
    });
    authState.isLoading = false;
    if (!error && data?.success) {
      authState.user = data?.profile;
      authState.isAuthenticated = true;
      authState.error = null;
      authState.lastLogin = new Date();
    }
    if (error) {
      throw error;
    }
    return data?.success;
  } catch (err) {
    authState.error = err;
    authState.isLoading = false;
    return false;
  }
}

export async function signout(): Promise<boolean> {
  try {
    const { error } = await logout();
    if (error) {
      throw error;
    }    
    authState.user = null;
    authState.isAuthenticated = false;
    authState.error = null;
    authState.lastLogin = null;
   return true;
  } catch (err: unknown) {
    authState.error = err;
    return false;
  }
}

export function setUser(user: Profile | null) {
  authState.user = user;
}


export function isSessionExpired(): boolean {
  if (!authState.lastLogin || !authState.isAuthenticated) return false; // No login or not authenticated, no expiration
  
  const now = new Date();
  const lastLogin = new Date(authState.lastLogin);
  const timeDiff = now.getTime() - lastLogin.getTime();
  
  const isExpired = timeDiff > SESSION_TIMEOUT;
  
  // If session is expired, reset the auth state
  if (isExpired) {
    console.log('Session expired, signing out');
    authState.user = null;
    authState.isAuthenticated = false;
    authState.error = getApiError("AKIN46009", {
      scope: "client",
      table: "auth",
    });
    authState.lastLogin = null;
    addToast({
      message: "Session expired",
      type: "error",
      dismissible: true,
      timeout: 3000,
    });
    return true;
  }
  
  return isExpired;
}

export function resetOnInit() {
  authState.isLoading = false;
  authState.error = undefined;
  
  // Check for session timeout
  isSessionExpired();
}

// Initialize auth state
if (typeof window !== 'undefined') {
  resetOnInit();
  // Check if we have a stored session
  if (authState.isAuthenticated && authState.user) {
    console.log('User is already authenticated');
  }
}

// Helper functions
export function requireAuth() {
  if (!authState.isAuthenticated) {
    throw new Error('Authentication required');
  }
}

export function isLoggedIn() {
  return authState.isAuthenticated && authState.user !== null;
}
