// src/lib/components/@akin/akinClient.ts
import { Api } from './Api';
import { config } from './../config';

/**
 * A singleton wrapper for the Akin API client
 */
export class AkinClient {
  private static instance: AkinClient;
  public readonly api: Api<string>;
  
  private constructor(baseUrl?: string, token?: string) {
    if(!baseUrl) {
      baseUrl = `${config.serverUrl}/api/v1`;
    }
    this.api = new Api<string>({
      baseUrl,
      securityWorker: (securityData) => {
        return {
          headers: {
            Authorization: `Bearer ${securityData}`,
          },
        };
      },
    });
    console.log(this.api)
    if (token) {
      this.setToken(token);
    }
  }
  
  /**
   * Get the AkinClient singleton instance
   */
  public static getInstance(baseUrl?: string, token?: string): AkinClient {
    if(!baseUrl) {
      baseUrl = `${config.serverUrl}/api/v1`;
    }
    if (!AkinClient.instance) {
      AkinClient.instance = new AkinClient(baseUrl, token);
    }
    return AkinClient.instance;
  }
  
  /**
   * Set the authentication token for API requests
   */
  public setToken(token: string | null): void {
    this.api.setSecurityData(token);
  }
  
  /**
   * Clear the authentication token
   */
  public clearToken(): void {
    this.api.setSecurityData('');
  }
  
}

// Create and export the singleton instance
export const akin = AkinClient.getInstance();

// For convenience when working with TypeScript
export type AkinApiType = Api<string>;