<script lang="ts">
  import {
    authState,
    instanceState,
    signout,
    subscriptionState,
    settingsState,
  } from "./states";
  // TODO finalize modularization of wp (move wp auth state to wp plugin)
  import { wpState } from "./plugins/wp/state.svelte";
  import SubscriptionButton from "./SubscriptionButton.svelte";
  import { formatTimeAgo } from "./utils/date";
  import Button from "./common/Button.svelte";
  import { addToast } from "./states";
  import Identity from "./Identity.svelte";
  import Linked from "./icons/Linked.svelte";
  import Notification from "./common/Notification.svelte";

  const handleLogout = async () => {
    const success = await signout();
    if (success) {
      addToast({
        message: authState.error.message,
        type: "error",
        dismissible: true,
        timeout: 3000,
      });
    }
  };
</script>

<div class="account-details">
  {#if instanceState.instance?.require_wp_user}
    {#if authState.isAuthenticated && wpState.wpIsLoggedIn}
      <p><Linked /> Great! We linked your wordpress account with akin.</p>
    {:else}
      <Notification type="warning" title="Beta test notice">
        <svelte:fragment slot="content">
          Please sign in to your wordpress account as well to use all features of
          akin. We are working on automatic signing you into to both services, atm.
          <a href={instanceState.instance?.root_url + "/login"}>Login to wordpress</a>
        </svelte:fragment>
      </Notification>
    {/if}
  {/if}
  <h2>Account & Subscription</h2>
  <section class="account-meta">
    <table>
      <tbody>
        <tr>
          <td>Full Name</td>
          <td>{authState?.user?.full_name}</td>
        </tr>
        <tr>
          <td>Username</td>
          <td>{authState?.user?.username}</td>
        </tr>
        <tr>
          <td>Website</td>
          <td>{authState?.user?.website}</td>
        </tr>
        <tr>
          <td>Avatar</td>
          <td>{authState?.user?.avatar_url || "https://placehold.co/64x64"}</td>
        </tr>
      </tbody>
    </table>
  </section>
  <section class="account-bio">
    <h3 class="account-bio-title">Bio</h3>
    {authState?.user?.bio || "No bio"}
  </section>
  <section class="subscription">
    <h3>Subscription</h3>
    {#if subscriptionState?.subscription}
      <p>
        You are subscribed to <a href={settingsState.serverUrl + "/" + instanceState.instance?.id}
          >{instanceState.instance?.name}</a
        >
        since {formatTimeAgo(
          new Date(subscriptionState?.subscription?.created_at!),
        )} as <span class="role">{subscriptionState?.subscription?.role}</span>
      </p>
    {/if}
    <SubscriptionButton />
  </section>
  <section class="permissions">
    <h3>Permissions</h3>
    <p>Manage and view the permissions for your subscription</p>
    <table>
      <tbody>
        <tr>
          <td>Moderation</td>
          <td>{authState?.user?.moderation}</td>
        </tr>
        <tr>
          <td>Profile Visibility</td>
          <td>{authState?.user?.privacy}</td>
        </tr>
      </tbody>
    </table>
  </section>
  <section class="identities">
    <h3>Linked Identities</h3>
    <p>
      You can link multiple identities to your account. To edit details, visit
      the linked service.
    </p>
    {#if authState?.user?.identities}
      {#each Object.keys(authState?.user?.identities || {}) as identity}
        <Identity
          identity={authState?.user?.identities?.[identity] as {
            provider: string;
            data: any;
            host: string;
          }}
        />
      {/each}
    {/if}
  </section>
  <section class="actions">
    <h3>Actions</h3>
    <Button onclick={handleLogout} aria-live="polite">
      <span>Logout</span>
    </Button>
  </section>
</div>

<style>
  .account-details {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    padding: 0;
    position: sticky;
    top: 4rem;
    font-family: var(--font-sans);
    background: var(--background-color);
    z-index: 1;
    border-radius: 3rem;
    padding: 1.5rem;
    border: 1px solid var(--color-grey-400);
  }
  .account-details h2 {
    font-size: 1.4rem;
    margin: 1rem 0 2rem 0;
  }
  section {
    margin-bottom: 2rem;
  }
  section h3 {
    margin-bottom: 0.5rem;
  }
  section p {
    margin-top: 0rem;
    font-size: 0.9rem;
  }
  table {
    width: 100%;
    border-collapse: collapse;
  }
  table tr {
    border-bottom: 1px solid var(--color-grey-400);
  }
  table tr td {
    padding: 0.5rem 0;
    font-size: 0.9rem;
  }
  table tr td:first-child {
    font-weight: 600;
    width: 6rem;
  }
  table tr:last-child {
    border-bottom: 0;
  }
  .role {
    text-transform: capitalize;
    font-weight: 600;
  }
</style>
