<script lang="ts">
  /**
   * ToggleSwitch component
   * A stylized toggle switch that can be used in forms to replace checkboxes
   */
  import { createEventDispatcher } from 'svelte';
  
  // Set up event dispatcher
  const dispatch = createEventDispatcher<{
    change: { checked: boolean }
  }>();

  // Export properties
  let { id, name, label, hint, checked = false, disabled = false } = $props<{
    id: string;            // ID for the input (required for label association)
    name: string;          // Name for the form field
    label?: string;        // Label text
    hint?: string;         // Helper text shown below the toggle
    checked?: boolean;     // Initial toggle state
    disabled?: boolean;    // Disable the toggle
  }>();
  
  // Create local reactive state for the toggle
  let isChecked = $state(checked);
  
  // Toggle the state
  function toggle() {
    if (disabled) return;
    isChecked = !isChecked;
    // Dispatch a typed change event
    dispatch('change', { checked: isChecked });
  }
  
  // Reference to the toggle button
  let toggleButton: HTMLButtonElement;
  
  // Update internal state when external checked prop changes
  $effect(() => {
    isChecked = checked;
  });
</script>

<div class="toggle-field {disabled ? 'disabled' : ''}">
  {#if label}
    <div class="toggle-label-wrapper">
      <label for={id}>{label}</label>
      {#if hint}
        <span class="field-hint">{hint}</span>
      {/if}
    </div>
  {/if}
  
  <div class="toggle-wrapper">
    <input 
      type="checkbox" 
      {id}
      {name}
      checked={isChecked}
      hidden
      disabled={disabled}
    />
    <button 
      type="button"
      class="toggle"
      aria-label="Toggle switch"
      role="switch"
      aria-checked={isChecked}
      onclick={toggle}
      data-checked={isChecked}
      bind:this={toggleButton}
    >
      <div 
        class="toggle-control"
        data-checked={isChecked}
      >
        <div 
          class="toggle-thumb"
          data-checked={isChecked}
        ></div>
      </div>
    </button>
  </div>
</div>

<style>
  /* Toggle component styles */
  .toggle-field {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
  }

  .toggle-label-wrapper {
    display: flex;
    flex-direction: column;
  }

  .toggle-wrapper {
    display: flex;
    align-items: center;
  }

  .toggle {
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .toggle-control {
    position: relative;
    width: 3rem;
    height: 1.5rem;
    border: 1px solid var(--clr-neutral-a20);
    border-radius: 1rem;
    transition: background-color 0.2s ease;
  }

  .toggle-thumb {
    position: absolute;
    left: 0.2rem;
    width: 1.1rem;
    height: 1.1rem;
    border-radius: 50%;
    border: 1px solid var(--clr-neutral-a20);
    transition: transform 0.2s ease;
    transform: translateY(-50%);
    top: 50%;
  }

  .toggle-thumb[data-checked="true"] {
    transform: translateX(1.5rem) translateY(-50%);
    background-color: var(--clr-primary-a0);
  }

  .field-hint {
    display: block;
    font-size: 0.8rem;
    color: var(--clr-neutral-a20);
    margin-top: 0.25rem;
    padding: 0;
  }
</style>
