<script lang="ts">
  import { uiState, setSidebarWidth, updateMaxWidth, applyRouteConstraints } from "./states/ui.svelte";
  import Advertisment from "./ads/Advertisment.svelte";
  import { onMount, onDestroy } from "svelte";
  import { location } from "svelte-spa-router";

  // Width control state

  const REM_TO_PX = 16;
  let isDragging = $state(false);
  let startX = $state(0);
  let startWidth = $state(0);

  let sidebarElement: HTMLElement | null = $state(null);

  // Resize handlers
  function startResize(e: MouseEvent) {
    isDragging = true;
    startX = e.clientX;
    startWidth = uiState.sidebarWidth;
    document.addEventListener("mousemove", handleMouseMove);
    document.addEventListener("mouseup", stopResize);
    document.body.style.cursor = "ew-resize";
    document.body.style.userSelect = "none";
  }

  function handleMouseMove(e: MouseEvent) {
    if (!isDragging) return;
    const delta = startX - e.clientX;
    const deltaRem = delta / REM_TO_PX;

    // Calculate new width
    const newWidth = startWidth + deltaRem;

    // Use the centralized width management system
    // This will handle skip ranges, min/max constraints, and screen bounds
    setSidebarWidth(newWidth, $location);
  }

  function stopResize() {
    isDragging = false;
    document.removeEventListener("mousemove", handleMouseMove);
    document.removeEventListener("mouseup", stopResize);
    document.body.style.cursor = "";
    document.body.style.userSelect = "";
  }

  // Reactively apply route constraints when location changes
  $effect(() => {
    if ($location) {
      applyRouteConstraints($location);
    }
  });

  // Window resize handler
  function handleWindowResize() {
    updateMaxWidth($location);
  }

  onMount(() => {
    // Set initial max width and listen for window resize
    updateMaxWidth($location);
    window.addEventListener('resize', handleWindowResize);
  });

  onDestroy(() => {
    // Clean up event listeners if component is destroyed while dragging
    document.removeEventListener("mousemove", handleMouseMove);
    document.removeEventListener("mouseup", stopResize);
    window.removeEventListener('resize', handleWindowResize);
  });
</script>

<aside
  bind:this={sidebarElement}
  class={`${uiState.sidebarOpen ? "open" : ""} position-right`}
  style={`width: ${uiState.sidebarWidth}rem;`}
>
  <div
    class="resize-handle"
    onmousedown={startResize}
    role="button"
    aria-label="Resize sidebar"
    tabindex="0"
  ></div>
  <div class="sidebar-content">
    <slot></slot>
  </div>
</aside>

<style>
  aside {
    display: block;
    align-self: stretch;
    transition:
      transform 0.3s ease,
      opacity 0.3s ease;
    z-index: 999;
    min-height: 100vh;
    background-color: transparent;
    position: relative; /* For positioning the resize handle */
    container: sidebar / inline-size;
  }

  .position-right {
    /* top: 0;
    right: 0; */
    display: none;
    /* height: 100vh; */
    /* transform: translateX(100%); */
    /* opacity: 0; */
  }

  .position-right.open {
    transform: translateX(0);
    display: block;
  }

  .sidebar-content {
    padding: 0 1rem 0 0;
    margin: 0;
  }

  .resize-handle {
    position: absolute;
    top: 0;
    left: 0;
    width: 6px;
    height: 100%;
    cursor: ew-resize;
    z-index: 0;
    background-color: transparent;
    transform: translateX(-3px);
    border-radius: 3px;
  }

  .resize-handle:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
</style>
