<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import {
    anchor,
    getYPositionFromElements,
    highlightRange,
    removeHighlights,
  } from "./utils/anchoring/html";
  import { convertToValidSelectors } from "./utils/anchoring/selector-helper";
  import { highlightColorManager } from "./utils/highlighting/colorAllocator";
  import ContextMenu from "./ContextMenu.svelte";
  import {
    annotationsState,
    setCurrentAnnotationId,
    setHoveredAnnotationId,
    setSidebarOpen,
    resetIndicators,
    addAnnotationToIndicator,
  } from "./states";
  import type { Annotation } from "./api";
  import type { Selector } from "./types/selectors";
  import { push } from "svelte-spa-router";

  interface Props {
    rootElement?: Element | null;
    highlightClass?: string;
  }

  let {
    rootElement = $bindable(null),
    highlightClass = "akin-highlight",
  }: Props = $props();

  let highlights = $state<Record<string, HTMLElement[]>>({});
  // We store highlights by annotation ID for easy lookup
  // No need for a separate annotation map
  let mounted = $state(false);
  // Track color assignments for annotations
  let colorAssignments = $state<Record<string, number>>({});

  // Context menu state
  let hoveredAnnotation = $state<Annotation | null>(null);
  let hoveredElement = $state<HTMLElement | null>(null);
  let showContextMenu = $state(false);

  // Helper functions for menu state management
  function showMenu(annotation: Annotation, element: HTMLElement) {
    hoveredAnnotation = annotation;
    hoveredElement = element;
    showContextMenu = true;
  }

  function hideMenu() {
    showContextMenu = false;
    hoveredAnnotation = null;
    hoveredElement = null;
  }

  async function applyHighlights() {
    if (!rootElement) return;

    removeHighlights(rootElement, highlightClass);
    resetIndicators()
    highlights = {};
    
    // Reset the color manager's range registry but keep existing color assignments
    highlightColorManager.clearRanges();
    
    // First pass: anchor all annotations to get their ranges
    const annotationRanges: { annotation: Annotation; range: Range }[] = [];
    
    for (const annotation of annotationsState.annotations) {
      const selectors = annotation.selectors as Selector[];
      if (!selectors) continue;
      
      try {
        const processedSelectors = convertToValidSelectors(selectors);
        const range = await anchor(rootElement, processedSelectors);
        annotationRanges.push({ annotation, range });


      } catch (error) {
        console.error(
          `Failed to create range for annotation ${annotation.id}:`,
          error,
        );
      }
    }
    
    // Second pass: apply highlights with color assignments
    for (const { annotation, range } of annotationRanges) {
      try {
        // Get color index for this annotation based on overlaps
        const colorIndex = highlightColorManager.getColorForAnnotation(annotation.id, range);
        colorAssignments[annotation.id] = colorIndex;
        
        // Create class names with both annotation ID and color class
        const annotationClass = `${highlightClass}-${annotation.id}`;
        const colorClass = `${highlightClass}-c${colorIndex}`;
        
        // Apply highlight with both classes
        const elements = highlightRange(
          range,
          `${highlightClass} ${annotationClass} ${colorClass}`,
        );
        
        highlights[annotation.id] = elements;
        
        // Add event listeners
        elements.forEach((el) => {
          el.dataset.annotationId = annotation.id;
          el.dataset.colorIndex = colorIndex.toString();
          
          el.addEventListener("click", (e) => {
            e.stopPropagation();
            hideMenu();
            push(`#/annotation/${annotation.id}`);
            setSidebarOpen(true); // Open sidebar
          });
        });
        // add annotation to indicator
        // first get the top most elements y position
        const yPosition = getYPositionFromElements(elements, rootElement);
        addAnnotationToIndicator(annotation, yPosition);
      } catch (error) {
        console.error(
          `Failed to highlight annotation ${annotation.id}:`,
          error,
        );
      }
    }
  }

  function handleOpenSidebar() {
    if (hoveredAnnotation) {
      hideMenu();
      setSidebarOpen(true);
      setCurrentAnnotationId(hoveredAnnotation.id);
    }
  }

  let previousAnnotations = $state("");
  let processing = $state(false);

  // Replace reactive statement with $effect
  $effect(() => {
    const annotationsJson = JSON.stringify(
      annotationsState.annotations.map((a) => a.id),
    );
    if (
      mounted &&
      rootElement &&
      annotationsJson !== previousAnnotations &&
      !processing
    ) {
      processing = true;
      previousAnnotations = annotationsJson;
      applyHighlights().finally(() => {
        processing = false;
      });
    }
  });

  // Find the real highlight element at a point (similar to Hypothesis's annotationsAtPoint)
  function getHighlightAtPoint(x: number, y: number): HTMLElement | null {
    // Get all elements at point
    const elements = document.elementsFromPoint(x, y);

    // Find the first element with the highlight class
    for (const el of elements) {
      if (el instanceof HTMLElement && el.classList.contains(highlightClass)) {
        return el;
      }
    }

    return null;
  }

  // Document-level event handlers (similar to Hypothesis's approach)
  function handleDocumentMouseOver(event: Event) {
    const mouseEvent = event as MouseEvent;
    if (!rootElement) return;

    const highlightEl = getHighlightAtPoint(
      mouseEvent.clientX,
      mouseEvent.clientY,
    );
    if (highlightEl) {
      const annotationId = highlightEl.dataset.annotationId;
      if (annotationId) {
        const annotation = annotationsState.annotations.find(
          (a) => a.id === annotationId,
        );
        if (annotation) {
          // Use the first highlight element for positioning the tooltip
          const elements = highlights[annotationId];
          if (elements && elements.length > 0) {
            // We use the first element as the anchor point for consistency
            showMenu(annotation, elements[0]);
            setHoveredAnnotationId(annotation.id);
          }
        }
      }
    } else if (showContextMenu) {
      // If we're not over a highlight, hide the menu
      hideMenu();
    }
  }

  function handleDocumentMouseOut(event: Event) {
    const mouseEvent = event as MouseEvent;
    // Only hide menu when not moving to another highlight of the same annotation
    const relatedTarget = mouseEvent.relatedTarget as HTMLElement;
    const leavingPage = !relatedTarget || !rootElement?.contains(relatedTarget);

    if (leavingPage || !relatedTarget?.classList?.contains(highlightClass)) {
      hideMenu();
      setHoveredAnnotationId(null);
    }
  }

  onMount(() => {
    if (!rootElement) {
      rootElement = document.body;
    }

    mounted = true;

    // Add document-level event handlers using event delegation
    // This is similar to how Hypothesis handles hover events
    if (rootElement) {
      rootElement.addEventListener("mouseover", handleDocumentMouseOver);
      rootElement.addEventListener("mouseout", handleDocumentMouseOut);
    }
  });

  onDestroy(() => {
    if (rootElement) {
      // Remove event listeners
      rootElement.removeEventListener("mouseover", handleDocumentMouseOver);
      rootElement.removeEventListener("mouseout", handleDocumentMouseOut);

      // Remove highlights
      removeHighlights(rootElement, highlightClass);
      
      // Reset color manager when component is destroyed
      highlightColorManager.reset();
    }
  });

  // Export function using $props for external access
  export function refresh() {
    applyHighlights();
  }
  
  // Export function to get color assignments for testing/debugging
  export function getColorAssignments() {
    return colorAssignments;
  }
</script>

<ContextMenu
  annotation={hoveredAnnotation}
  anchorElement={hoveredElement}
  visible={showContextMenu}
  onSidebarOpen={handleOpenSidebar}
  onClose={hideMenu}
/>

<style>
  :host {
    display: contents;
  }
</style>
