<script lang="ts">
  import {
    subscriptionState,
    subscribe,
    unsubscribe,
  } from "./states";
  import Notification from "./common/Notification.svelte";
  import Button from "./common/Button.svelte";

  const handleSubscribe = () => {
    subscribe();
  };

  const handleUnsubscribe = () => {
    unsubscribe();
  };
</script>

<div>
  {#if subscriptionState.subscription && subscriptionState.subscription?.moderation === "approved"}
    <Button type="button" variant="primary" onclick={handleUnsubscribe}
      >{subscriptionState.isLoading
        ? "Unsubscribing..."
        : `Cancel Subscription`}</Button
    >
  {:else if subscriptionState.subscription && subscriptionState.subscription?.moderation === "pending"}
    <Button type="button" variant="primary" onclick={handleSubscribe}
      >{subscriptionState.isLoading
        ? "Cancelling..."
        : `Cancel Request`}</Button
    >
  {:else}
    <Button type="button" variant="default" onclick={handleSubscribe}
      >{subscriptionState.isLoading
        ? "Subscribing..."
        : `Subscribe`}</Button
    >
  {/if}
  {#if subscriptionState.error}
    <Notification type="error" title="Subscription error">
      <svelte:fragment slot="content"
        >{subscriptionState.error.message}</svelte:fragment
      >
    </Notification>
  {/if}
</div>
