import './akin.css'
import Akin from './Akin.svelte';
import { config } from './config';
import { sanitizeUrl } from './utils/url';

// Entrance point for build process of web component
const akin = document.querySelector('ak-in');

if ( akin ) {
    // do nothing, and try to use the component with the attributes
    console.log('Akin web component found, trying to use it');
} else {
    console.log('Akin web component not found, building it');
    const akin = document.createElement('ak-in');
    if(config.serverUrl) akin.setAttribute('serverurl', config.serverUrl);
    akin.setAttribute('pageurl', sanitizeUrl(window.location.href));

    // Prepare DOM for akin injection
    const bodyChildren = Array.from(document.body.children);
    const wrapper = document.createElement('div');
    wrapper.className = 'akin-body-wrapper';
    bodyChildren.forEach(child => {
      wrapper.appendChild(child);
    });
    
    document.body.appendChild(wrapper);
    document.body.appendChild(akin);

    document.body.style.display = 'flex';
    document.body.style.flexDirection = 'row';
    document.body.style.justifyContent = 'space-between';
    document.body.style.alignItems = 'stretch';
}