/**
 * Toasts state management
 */

export type Toast = {
    id: number;
    type: 'info' | 'success' | 'warning' | 'error';
    message: string;
    dismissible?: boolean;
    timeout?: number;
};

export let toastsState = $state(
    { toasts: [] as Toast[] }
);

/**
 * Add a new toast to the toasts collection
 */
export function addToast({
    message,
    type = 'info',
    dismissible = true,
    timeout = 3000 }: Partial<Toast>): number {
    if (!message) {
        return -1;
    }
    const newToast: Toast = {
        id: Math.floor(Math.random() * 10000),
        type,
        message,
        dismissible,
        timeout,
    }

    toastsState.toasts = [...toastsState.toasts, newToast];

    if (newToast.timeout) setTimeout(() => dismissToast(newToast.id), newToast.timeout);
    return newToast.id;
}

/**
 * Remove a toast by its ID
 */
export function dismissToast(id: number) {
    toastsState.toasts = toastsState.toasts.filter((t) => t.id !== id);
}
